import React, {Component} from 'react';
import {LavenDatePicker} from '@components/DatePicker';
import {getAge} from '@functions/Global';
import API from '@api';

import "@styles/components/Forms.scss";

export class BillForm extends Component {
    constructor(props) {
        super(props);

        let ngay_sinh_be = this.props.data ? this.props.data.ngay_sinh_be : null;
        ngay_sinh_be = ngay_sinh_be ? ngay_sinh_be.split("-")[0].length == 2 ? ngay_sinh_be : ngay_sinh_be.split("-")[2] + '-' + ngay_sinh_be.split("-")[1] + '-' + ngay_sinh_be.split("-")[0] : null

        let ngay_chup = this.props.date ? this.props.date : this.props.data ? this.props.data.ngay_chup : null
        ngay_chup = ngay_chup ? ngay_chup.split("-")[0].length == 2 ? ngay_chup : ngay_chup.split("-")[2] + '-' + ngay_chup.split("-")[1] + '-' + ngay_chup.split("-")[0] : null

        let ngay_giao_hinh = this.props.data ? this.props.data.ngay_giao_hinh : null;
        ngay_giao_hinh = ngay_giao_hinh ? ngay_giao_hinh.split("-")[0].length == 2 ? ngay_giao_hinh : ngay_giao_hinh.split("-")[2] + '-' + ngay_giao_hinh.split("-")[1] + '-' + ngay_giao_hinh.split("-")[0] : null

        this.state = {
            staff: null,
            branch: null,
            status: null,
            date: ngay_chup,
            birthday: ngay_sinh_be,
            ngay_giao_hinh: ngay_giao_hinh,
        }

        this.fetchData();
    }

    async fetchData() {
        await API.Staff.getAll().then(res => {
            this.setState({
                staff: res.data
            })
        })

        await API.Branch.getAll().then(res => {
            this.setState({
                branch: res.data
            })
        });

        await API.Status.getAll().then(res => {
            this.setState({
                status: res.data
            })
        });

        if(this.state.birthday) this.handleChangeBirthday(this.state.birthday);
    }
    
    handleSave = () => {
        let data = {
            chi_nhanh: this.refs.chi_nhanh.value,
            gio_chup: this.refs.gio_chup.value,
            ngay_chup: this.state.date ? this.state.date : new Date(),
            ghi_chu: this.refs.ghi_chu.value,
            photo: this.refs.photo.value || null,
            support: this.refs.support.value || null,
            photoshop: this.refs.photoshop.value || null,
            consultant: this.refs.consultant.value || null,
            ten_khach_hang: this.refs.ten_khach_hang.value,
            dia_chi: this.refs.dia_chi.value,
            ho_ten_be: this.refs.ho_ten_be.value,
            ngay_sinh_be: this.state.birthday,
            gioi_tinh_be: this.refs.gioi_tinh_be.value,
            so_tuoi: this.refs.so_tuoi.value,
            so_dien_thoai: this.refs.so_dien_thoai.value,
            tinh_trang: this.refs.tinh_trang.value,
            ngay_giao_hinh : this.state.ngay_giao_hinh
        }

        this.props.onSave(data);
    }

    onTagsChange = (e, value) => {
        
    }

    handleChange = (date) => {
        this.setState({
          date: date
        });
    }

    handleChangeBirthday = (date) => {
        let new_date = date.split("-");
        new_date = new_date[0].length !== 2 ? date : new_date[2] + '-' + new_date[1] + '-' + new_date[0];

        let age = getAge(new Date(new_date));
        this.refs.so_tuoi.value = age

        this.setState({
          birthday: date
        });
    }

    handleChangeNgayGiaoHinh = (date) => {
        this.setState({
            ngay_giao_hinh: date
        });
    }

    render() {
        const chuc_vu = localStorage.getItem("chuc_vu");

        if(!this.state.staff || !this.state.branch || !this.state.status) {
            return(<div></div>)
        }

        return(
            <div className="bill_form_container">
                <div className="column">
                    <div className="box">
                        <span className="title">Chi Nhnh:</span>
                        <select defaultValue={this.props.data ? this.props.data.chi_nhanh._id : "default"} ref="chi_nhanh" disabled={this.props.perms.bill[2] == "1" ?  null : 'disabled'}>
                            <option disabled selected value="default"></option>
                            {this.state.branch.map((data, i) => {
                                return (
                                    <option value={data._id} key={i}>{data.ten_chi_nhanh}</option>
                                )
                            })}
                        </select>
                    </div>
                    <div className="box">
                        <span className="title">Gi Chp:</span>
                        <input className="textbox" defaultValue={this.props.data ? this.props.data.gio_chup : ""} ref="gio_chup" type="time" disabled={this.props.perms.bill[2] == "1" ?  null : 'disabled'}/>
                    </div>
                    <div className="box">
                        <span className="title">Ngy Chp:</span>
                        <LavenDatePicker format={'DD-MM-YYYY'} date={this.state.date} ref="ngay_chup" className="textbox" onChange={this.handleChange} disabled={this.props.perms.bill[2] == "1" ?  null : 'disabled'}/>
                    </div>
                    <div className="box textarea">
                        <span className="title">Ghi Ch:</span>
                        <textarea className="textbox" defaultValue={this.props.data ? this.props.data.ghi_chu : ""} ref="ghi_chu"></textarea>
                    </div>
                    <div className="box">
                        <span className="title">Photo:</span>
                        <select ref="photo" defaultValue={this.props.data ? this.props.data.photo : ""} disabled={this.props.perms.bill[2] == "1" ?  null : 'disabled'}>
                            <option disabled selected value=""></option>
                            {this.state.staff.map((data, i) => {
                                return (
                                    <option value={data._id} key={i}>{data.ho_ten}</option>
                                )
                            })}
                        </select>
                    </div>
                    <div className="box">
                        <span className="title">Support:</span>
                        <select ref="support" defaultValue={this.props.data ? this.props.data.support : ""} disabled={this.props.perms.bill[2] == "1" ?  null : 'disabled'}>
                            <option disabled selected value=""></option>
                            {this.state.staff.map((data, i) => {
                                return (
                                    <option value={data._id} key={i}>{data.ho_ten}</option>
                                )
                            })}
                        </select>
                    </div>
                    <div className="box">
                        <span className="title">T Vn:</span>
                        <select ref="consultant" defaultValue={this.props.data ? this.props.data.consultant : ""} disabled={this.props.perms.bill[2] == "1" ?  null : 'disabled'}>
                            <option disabled selected value=""></option>
                            {this.state.staff.map((data, i) => {
                                return (
                                    <option value={data._id} key={i}>{data.ho_ten}</option>
                                )
                            })}
                        </select>
                    </div>
                    <div className="box">
                        <span className="title">Photoshop:</span>
                        <select ref="photoshop" defaultValue={this.props.data ? this.props.data.photoshop : ""} disabled={this.props.perms.bill[2] == "1" ?  null : 'disabled'}>
                            <option disabled selected value=""></option>
                            {this.state.staff.map((data, i) => {
                                return (
                                    <option value={data._id} key={i}>{data.ho_ten}</option>
                                )
                            })}
                        </select>
                    </div>
                    <div className="box">
                        <span className="title">Tnh Trng:</span>
                        <select ref="tinh_trang" defaultValue={this.props.data ? this.props.data.tinh_trang : ""}>
                            <option disabled selected value=""></option>
                            {this.state.status.map((data, i) => {
                                return (
                                    <option value={data._id} key={i}>{data.ten_tinh_trang}</option>
                                )
                            })}
                        </select>
                    </div>
                </div>

                <div className="column">
                    <div className="box">
                        <span className="title">Tn Khch Hng:</span>
                        <input className="textbox" defaultValue={this.props.data ? this.props.data.ten_khach_hang : ""} ref="ten_khach_hang" type="text" disabled={this.props.perms.bill[2] == "1" ?  null : 'disabled'}/>
                    </div>
                    <div className="box">
                        <span className="title">S in thoi:</span>
                        <input className="textbox" defaultValue={this.props.data ? this.props.data.so_dien_thoai : ""} ref="so_dien_thoai" type="number" disabled={this.props.perms.bill[2] == "1" ?  null : 'disabled'}/>
                    </div>
                    <div className="box">
                        <span className="title">a Ch:</span>
                        <input className="textbox" defaultValue={this.props.data ? this.props.data.dia_chi : ""} ref="dia_chi" type="text" disabled={this.props.perms.bill[2] == "1" ?  null : 'disabled'}/>
                    </div>
                    <div className="box">
                        <span className="title">H Tn B:</span>
                        <input className="textbox" defaultValue={this.props.data ? this.props.data.ho_ten_be : ""} ref="ho_ten_be" type="text" disabled={this.props.perms.bill[2] == "1" ?  null : 'disabled'}/>
                    </div>
                    <div className="box">
                        <span className="title">Ngy Sinh B:</span>
                        <LavenDatePicker format={'DD-MM-YYYY'} date={this.state.birthday} ref="ngay_sinh_be" className="textbox" onChange={this.handleChangeBirthday} disabled={this.props.perms.bill[2] == "1" ?  null : 'disabled'}/>
                    </div>
                    <div className="box">
                        <span className="title">Gii Tnh B:</span>
                        <select ref="gioi_tinh_be" defaultValue={this.props.data ? this.props.data.gioi_tinh_be : ""} disabled={this.props.perms.bill[2] == "1" ?  null : 'disabled'}>
                            <option disabled selected="selected">Gii tnh b: </option>
                            <option value="0">B trai</option>
                            <option value="1">B gi</option>
                        </select>
                    </div>
                    <div className="box">
                        <span className="title">S Tui:</span>
                        <input className="textbox" defaultValue={this.props.data ? this.props.data.so_tuoi : ""} ref="so_tuoi" type="text" disabled/>
                    </div>
                    <div className="box">
                        <span className="title">Ngy Giao Hnh:</span>
                        <LavenDatePicker format={'DD-MM-YYYY'} date={this.state.ngay_giao_hinh} ref="ngay_giao_hinh" className="textbox" onChange={this.handleChangeNgayGiaoHinh}/>
                    </div>
                </div>
            </div>
        )
    }
}