import Socket from '@socket';
import Axios from '@api/Axios';

const socket = new Socket();

const BillAPI = {
    add: async(data) => {
        let res = await Axios.post('bills', data);
        socket.Bill().emitAdd(data);

        return res.data;
    },
    get: async(id) => await Axios.get(`bills/${id}`),
    download: async() => await Axios.get(`bills/download`),
    getAll: async() => await Axios.get(`bills`),
    update: async(id, data) => {
        await Axios.put(`bills/${id}`, data).then(res => {
            socket.Bill().emitUpdate({
                _id: id,
                ...data
            });

            return res.data;
        }).catch(e => {
            throw new Error(e);
        })
    },
    remove: async(id) => {
        await Axios.delete(`bills/${id}`).then(res => {
            socket.Bill().emitRemove(id);

            return res.data;
        }).catch(e => {
            throw new Error(e);
        });
    },
}

export default BillAPI;