import React, { useEffect, useState, useRef } from 'react';
import API from '@api';
import { Link, Redirect } from 'react-router-dom';

const SignupScreen = () => {
    const emailRef = useRef(null);
    const passwordRef = useRef(null);
    const nameRef = useRef(null);
    const companyNameRef = useRef(null);
    const companyAddressRef = useRef(null);
    const companyPhoneRef = useRef(null);
    const [signupMsg, setSignupMsg] = useState(null);
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [companySlug, setCompanySlug] = useState('');

    useEffect(() => {
        if (localStorage.getItem('JWT')) {
            API.User.verifyToken().then(res => {
                setCompanySlug(res.data.company_slug);
                setIsLoggedIn(true);
            }).catch(e => console.log(e));
        }
    }, []);

    const _handleSignup = async () => {
        let email = emailRef.current.value;
        let password = passwordRef.current.value;
        let name = nameRef.current.value;
        let companyName = companyNameRef.current.value;
        let companyAddress = companyAddressRef.current.value;
        let companyPhone = companyPhoneRef.current.value;

        let res = await API.Company.add(companyName, companyAddress, companyPhone, name, email, password).catch(e => console.log(e));

        if (res.code === 200) {
            //*Save storage
            localStorage.setItem('JWT', res.data.accessToken);
            API.User.verifyToken().then(res => {
                setCompanySlug(res.data.company_slug);
                
                window.location.reload();
            }).catch(e => console.log(e));
        }else if(res.code === 400) {
            setSignupMsg("Cng ty  c to!");
        }else{
            console.log(res);
        }
    }

    if (isLoggedIn) {
        return <Redirect to={`/${companySlug}/`}/>
    }

    return(
        <div style={{display: 'flex', flexDirection: 'column', width: '100%', height: '100vh', alignItems: 'center', justifyContent: 'center'}}>
            <h1>ng K</h1>

            {
                signupMsg ?
                <a style={{marginBottom: "16px", fontSize: "14px", color: "red"}}>{signupMsg}</a> : null
            }

            <div style={{
                display: 'flex',
                flexDirection: 'column',
                gap: 16
            }}>
                <input type="text" ref={companyNameRef} placeholder="Tn cng ty" className="textbox"/>
                <input type="address" ref={companyAddressRef} placeholder="a ch" className="textbox"/>
                <input type="phone" ref={companyPhoneRef} placeholder="S in thoi" className="textbox"/>
                <input type="name" ref={nameRef} placeholder="H tn" className="textbox"/>
                <input type="email" ref={emailRef} placeholder="Email" className="textbox"/>
                <input type="password" ref={passwordRef}  placeholder="Mt khu" className="textbox"/>
                <button className="btn blue" onClick={_handleSignup}>ng K</button>

                <Link
                    to="/login"
                    style={{
                        fontSize: 12,
                        color: "#2484ff",
                        textDecoration: "none",
                        marginTop: 5
                    }}
                >Bn  c ti khon? Hy ng nhp</Link>
            </div>
        </div>
    )
}

export default SignupScreen;