import React, {Component} from 'react';
import {GrClose} from 'react-icons/gr';
import {Link} from 'react-router-dom';
import {findWithAttr} from '../functions/Global';
import {LavenDatePicker} from '../components/DatePicker';
import {HashSpiner} from './Spiners';
import API from '@api';

import "../styles/components/Modals.scss";

class PermissionModal extends Component {
    handleSave = () => {
        API.Permission.add(this.refs.ten_nhom.value).then(res => {
            this.props.onCreateNew();
        })
    }

    render() {
        return(
            <div className="modal" style={{display: `${this.props.is_open === "true" ? "flex" : "none"}`}}>
                <div className="form_container">
                     <h1>Thm Nhm Quyn</h1>
                     <div className="box">
                         <span className="title">Tn nhm</span>
                         <input type="text" className="textbox" ref="ten_nhom"/>
                     </div>

                     <div className="column">
                         <button className="btn" onClick={this.props.handleCloseModal}>Hy</button>
                         <button className="btn blue" onClick={this.handleSave}>Lu</button>
                     </div>
                </div>
            </div>
        )
    }
}

class PermissionEditModal extends Component {
    constructor(props) {
        super(props);

        this.state = {
            perms: null
        }

        API.Permission.getUserPerm().then(res => {
            this.setState({
                perms: res.data.perms
            })
        });
    }

    handleSave = () => {
        API.Permission.update(this.props._id, {
            ten_nhom: this.refs.ten_nhom.value,
        }).then(res => {
            this.props.handleSave();
        });
    }

    handleRemove = () => {
        API.Permission.remove(this.props._id).then(res => {
            this.props.handleSave();
        });
    }

    render() {
        const perms = this.state.perms;

        if(!this.state.perms){
            return(<div></div>)
        }
        return(
            <div className="modal" style={{display: `${this.props.is_open === "true" ? "flex" : "none"}`}}>
                <button className="close_btn" onClick={this.props.handleCloseModal}>
                    <GrClose className="icon"/>
                </button>
                <div className="form_container">
                     <h1>Sa Phn Quyn</h1>
                     <div className="box">
                         <span className="title">Tn nhm</span>
                         <input type="text" className="textbox" ref="ten_nhom" defaultValue={this.props.ten_nhom}/>
                     </div>

                     {this.props.ten_nhom.toLowerCase() !== "admin" ? //Tn nhm khng phi admin th cho chnh sa
                     <div className="column">
                        {
                            perms.permissions[3] == "1" ? 
                            <button className="btn red" onClick={this.handleRemove}>Xa</button> : null }
                        <button className="btn blue" onClick={this.handleSave}>Lu</button>
                     </div> : <div className="column">
                        <button className="btn red" onClick={this.props.handleCloseModal}>Hy</button>
                     </div>}
                </div>
            </div>
        )
    }
}

class BillServicesModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            goi_chup: null
        }

        API.PackageType.getSlug('goi_chup').then(package_type => {
            API.Package.getType(package_type.data._id).then(res => {
                let goi_chup_data = res.data;

                API.PackageType.getSlug('dich_vu').then(package_type => {
                    API.Package.getType(package_type.data._id).then(res => {
                        let dich_vu_data = res.data;
                        
                        this.setState({
                            goi_chup: [
                                ...goi_chup_data,
                                ...dich_vu_data
                            ]
                        })
                    }).catch(e => console.log(e));
                }).catch(e => console.log(e));
            }).catch(e => console.log(e));
        }).catch(e => console.log(e));
    }

    handleSave = () => {
        let data = this.state.goi_chup.filter((data, i) => {
            return data._id == this.refs.ten_san_pham.value
        });

        let giam_gia = parseInt(this.refs.giam_gia.value.toString().replace(/\.|\,/g,''))

        let new_data = {
            chiet_khau: 0,
            so_luong: this.refs.so_luong.value,
            _id: data[0]._id,
            ten_goi: data[0].ten_goi,
            giam_gia: giam_gia,
            gia: data[0].gia
        }

        this.props.onSave(new_data);
    }

    render() {
        if(!this.state.goi_chup) {
            return(<div></div>)
        }
        return(
            <div className="modal" style={{display: `${this.props.is_open === "true" ? "flex" : "none"}`}}>
                <div className="form_container">
                     <h1>Thm Sn Phm</h1>
                    
                     <div className="box" style={{width: "215px"}}>
                        <span className="title">Tn sn phm</span>
                        <select ref="ten_san_pham">
                            {this.state.goi_chup.map((data, i) => {
                                return <option value={data._id}>{data.ten_goi}</option>  
                            })}
                        </select>
                    </div>

                     <div className="box">
                         <span className="title">S lng</span>
                         <input type="number" className="textbox" ref="so_luong" min="0" defaultValue="1"/>
                     </div>

                    <div className="box">
                        <span className="title">Gim gi</span>
                        <input type="number" className="textbox" ref="giam_gia" min="0" defaultValue={0}/>
                    </div>

                     <div className="column">
                         <button className="btn" onClick={this.props.onClose}>Hy</button>
                         <button className="btn blue" onClick={this.handleSave}>Lu</button>
                     </div>
                </div>
            </div>
        )
    }
}

class CalendarModal extends Component {
    render() {
        if(this.props.data == undefined) {
            return <div></div>
        }

        let data = this.props.data;
        let ngay_chup = data.ngay_chup.split("-");

        return(      
            <div className="modal m_calendar" style={{display: `${this.props.visible ? "flex" : "none"}`}}>
                <button className="close_btn" onClick={this.props.handleCloseModal}>
                    <GrClose className="icon"/>
                </button>
                <div className="form_container">
                    <h1>{data.title}</h1>
                    <span>Ngy chp: {ngay_chup[0]} thng {ngay_chup[1]} nm {ngay_chup[2]}</span>
                    <span>Gi chp: {data.gio_chup}</span>
                    <div class="column">
                        <button class="btn" onClick={this.props.handleCloseModal}>ng</button>
                        <Link to={`bill_edit/${data._id}`}><button class="btn blue">Sa</button></Link>
                    </div>
                </div>
            </div>
        )
    }
}

class MobileCalendarModal extends Component {
    render() {
        if(this.props.data == undefined) {
            return <div></div>
        }

        return(      
            <div className="modal m_calendar" style={{display: `${this.props.visible ? "flex" : "none"}`}}>
                <button className="close_btn" onClick={this.props.handleCloseModal}>
                    <GrClose className="icon"/>
                </button>
                <div className="form_container">
                    <h1>Xem Lch</h1>
                    {this.props.data.map((data, i) => {
                        return(
                            <Link to={`bill_edit/${data._id}`} className={`event_card ${findWithAttr(this.props.branch,'ten_chi_nhanh',data.branch) > -1 ? this.props.branch[findWithAttr(this.props.branch,'ten_chi_nhanh',data.branch)].color : ''}`}>
                                <span className="date">{data.ngay_chup} {data.gio_chup}</span>
                                <p className="content">{data.title}</p>
                            </Link>
                        )
                    })}
                </div>
            </div>
        )
    }
}

export {
    PermissionModal,
    BillServicesModal,
    PermissionEditModal,
    CalendarModal,
    MobileCalendarModal,
}