//*DATE FORMAT
Object.defineProperty(Date.prototype, 'format', {
    value: function (format) {
        let date = this;
        let dateFormated = format;

        let dateNow = date.getDate();
        if(dateNow <= 9) dateNow = '0' + dateNow;

        let monthNow = date.getMonth() + 1;
        if(monthNow <= 9) monthNow = '0' + monthNow;

        let yearNow = date.getFullYear();

        dateFormated = dateFormated.replaceAll("dd", dateNow.toString());
        dateFormated = dateFormated.replaceAll("MM", monthNow);
        dateFormated = dateFormated.replaceAll("yyyy", yearNow);

        return dateFormated;
    }
});

//*GET AGE
Object.defineProperty(Date.prototype, 'getAge', {
    value: function() {
        var ageDifMs = Date.now() - this.getTime();
        var ageDate = new Date(ageDifMs); // miliseconds from epoch
        return Math.abs(ageDate.getUTCFullYear() - 1970);
    }
});