import React, {useEffect, useState, useRef} from 'react';
import {Link} from 'react-router-dom';
import API from "@api";
import { BsList } from 'react-icons/bs';

import "./styles.scss";

import {sidebarItems} from './items';

const BranchSelect = () => {
    const [branch, setBranch] = useState([]);
    const [fBranch, setFBranch] = useState({});
    const [branchSelected, setBranchSelected] = useState([]);
    const [dropdownVisible, setDropdownVisible] = useState(false);
    const wrapperRef = useRef(0);

    useEffect(() => {
        fetchData();

        document.addEventListener("mousedown", handleClickOutside);

        return () => {
            document.removeEventListener("mousedown", handleClickOutside);
        }
    }, []);

    const fetchData = async () => {
        //Fetch branch
        let branch = await API.Branch.getAll();
        setBranch(branch.data);

        //Get branch selected
        let branchSelected = JSON.parse(localStorage.getItem("branch_selected") || "[]");
        branchSelected = branchSelected.length ? branchSelected : [branch.data[0]._id];

        setBranchSelected(branchSelected);
        
        //Select default branch
        handleSelectBrach(branchSelected[0], branch.data);
    }

    const branchSelectBoxChange = (e, branchId) => {
        let branchSelectedId = "";
        let branchSelectedTemp = [...branchSelected];

        //Update list
        if(e.currentTarget.checked) { //Push
            if(branchSelectedTemp.indexOf(branchId) <= -1) {
                branchSelectedTemp = [...branchSelectedTemp, branchId]
                branchSelectedId = branchId;
            }
        }else{ //Remove
            branchSelectedTemp = [...branchSelectedTemp];
            branchSelectedTemp.splice(branchSelectedTemp.indexOf(branchId), 1);
        }

        //UPdate state
        setBranchSelected(branchSelectedTemp);

        //Update localStorage
        console.log(branchSelectedTemp);
        localStorage.setItem("branch_selected", JSON.stringify(branchSelectedTemp));

        if(branchSelectedTemp.length) handleSelectBrach(branchSelectedTemp[0]);
    }
    
    const handleSelectBrach = (branchId, branchList=branch) => {
        let branchSelectedData = branchList.find(b => b._id == branchId);
        let branchCode = branchSelectedData.ten_chi_nhanh.split(" ");
        branchCode = `${branchCode[0].charAt(0).toUpperCase()}${branchCode[1]?.charAt(0).toUpperCase()}`;

        let fBranch = {
            code: branchCode,
            name: branchSelectedData.ten_chi_nhanh,
            color: branchSelectedData.color
        };

        setFBranch(fBranch);
    }

    const handleClickOutside = e => {
        if (wrapperRef && !wrapperRef?.current?.contains(e?.target)) {
            handleCloseDropdown();
        }
    }

    const handleCloseDropdown = () =>{
        setDropdownVisible(false);
    }

    return (
        <div className="branch-select-box" ref={wrapperRef}>
            <div className="branch-select-box__select" onClick={() => setDropdownVisible(!dropdownVisible)}>
                <div className="branch-select-box__select-list">
                    {
                        branch.map((b, index) => {
                            if(branchSelected.indexOf(b._id) <= -1) return;

                            let branchCode = b.ten_chi_nhanh.split(" ");
                            branchCode = `${branchCode[0].charAt(0).toUpperCase()}${branchCode[1]?.charAt(0).toUpperCase()}`;

                            return (
                                <div 
                                    className="branch-select-box__select-group__avatar"
                                    style={{
                                        backgroundColor: b.color
                                    }}
                                >{branchCode || "CN"}</div>
                            )
                        })
                    }
                </div>
            </div>
            <div className={`branch-select-box__dropdown${dropdownVisible ? '--visible': ''}`}>
                {
                    branch.map((b, index) => {
                        let branchCode = b.ten_chi_nhanh.split(" ");
                        branchCode = `${branchCode[0].charAt(0).toUpperCase()}${branchCode[1]?.charAt(0).toUpperCase()}`;
                        
                        return (
                            <label
                                className="branch-select-box__select-group" 
                                htmlFor={`checkbox-${b._id}`} 
                                key={`checkbox-${b._id}`}
                            >
                                <input checked={branchSelected.indexOf(b._id) > -1} onChange={e => branchSelectBoxChange(e, b._id)} className="branch-select-box__select-group__checkbox" id={`checkbox-${b._id}`} type="checkbox"/>
                                <div 
                                    className="branch-select-box__select-group__avatar"
                                    style={{
                                        backgroundColor: b.color
                                    }}
                                >{branchCode}</div>
                                <span className="branch-select-box__select-group__name">{b.ten_chi_nhanh || "CN"}</span>
                            </label>
                        )
                    })
                }
                <button className="btn blue" onClick={() => window.location.reload()}>Lu</button>
            </div>
        </div>
    )
}

export const SideBar = () => {
    const [location, setLocation] = useState(window.location.pathname);
    const [userPermission, setUserPermission] = useState(null);
    const [minimize, setMinimize] = useState(true);
    const [companySlug, setCompanySlug] = useState('');

    useEffect(() => {
        API.Permission.getUserPerm().then(res => {
            setUserPermission(res.data);
        });

        API.User.verifyToken().then(res => {
            setCompanySlug(res.data.company_slug);
        });
    }, []);

    const handleToggleSideBar = () => {
        localStorage.setItem("sidebar_minized", !minimize);
        setMinimize(!minimize);
    }
    
    const perms = userPermission?.perms;

    if(!perms) return null;

    return(
    <div className={`side_bar_container ${minimize ? 'min' : ''}`}>
        <div className={`side_bar`}>
            <div className='item avatar_container'>
                <button className="menu_btn" onClick={handleToggleSideBar}><BsList className="icon"/></button>
                <p className='user_name'>{localStorage.getItem('ho_ten')}</p>
            </div>

            {!minimize && <BranchSelect/>}

            {
                sidebarItems.map((item, index) => {
                    let permCheck = 0;

                    if(item.perms?.length) {
                        item.perms.map((perm) => {
                            if(permCheck >= 0) {
                                let key = Object.keys(perm)[0];
                                let permIndex = Object.values(perm)[0];
                                
                                if(perms[key][permIndex]) {
                                    permCheck++;
                                }else{
                                    permCheck = -1;
                                }
                            }
                        });
                    }else{
                        permCheck ++;
                    }

                    if(item.isDivider) return <br/>

                    if(permCheck > 0) {
                        if(item.isHeader) {
                            return (
                                <div className="menu_header" key={`sidebar-item-${index}`}>
                                    {
                                        minimize ? 
                                        <hr/> : <span>{item.name}</span>
                                    }
                                </div>
                            )
                        }
                        return (
                            <Link 
                                to={`/${companySlug}${item.path}`}
                                key={`sidebar-item-${index}`} 
                                className={location === `/${companySlug}${item.path}` ? 'item active' : 'item'} 
                                onClick={() => {
                                    setMinimize(true);
                                    setLocation(`/${companySlug}${item.path}`);
                                }}
                            >
                                <div className="icon">{item.icon}</div>
                                <p className="item_name">{item.name}</p>
                            </Link>
                        )
                    }
                })
            }
        </div>
    </div>
    )
}