import React,{ Component } from 'react';
import {SettingsHeader} from '@components/Header_bar';
import {Link} from 'react-router-dom';

import * as Ionicons from 'react-icons/io5';
import "@styles/views/Settings.scss";

import {BsPeople, BsAppIndicator, BsInboxes, BsBuilding, BsCreditCard, BsFillArchiveFill, BsPersonCheck, BsFolderCheck} from 'react-icons/bs';

import API from '@api';

class SettingScreen extends Component {
    constructor(props) {
        super(props);
        this.state = {
            perms: null
        } 

        API.Permission.getUserPerm().then(res => {
            this.setState({
                perms: res.data.perms
            })
        }).catch(e => console.log(e));
    }

    render() {
        if(!this.state.perms) {
            return(<div></div>)
        }
        return(
            <div className="settings_view">
                <SettingsHeader/>
                <div className="settings_container">
                    <Link to="settings/profile" className="setting_card">
                        <div className="icon_container">
                            <Ionicons.IoPersonOutline className="icon"/>
                        </div>
                        <span className="setting_title">Thng Tin C Nhn</span>
                    </Link>
                    <Link to="settings/personnel" className="setting_card">
                        <div className="icon_container">
                            <BsPeople className="icon"/>
                        </div>
                        <span className="setting_title">Nhn S</span>
                    </Link>
                    {this.state.perms.branch[0] == "1" ? 
                    <Link to="branch" className="setting_card">
                        <div className="icon_container">
                            <BsBuilding className="icon"/>
                        </div>
                        <span className="setting_title">Chi Nhnh</span>
                    </Link> : null}
                    
                    {this.state.perms.status[0] == "1" ?
                    <Link to="status" className="setting_card">
                        <div className="icon_container">
                            <BsAppIndicator className="icon"/>
                        </div>
                        <span className="setting_title">Tnh Trng</span>
                    </Link> : null}

                    
                    {this.state.perms.backup[0] == "1" ?
                    <Link to="subscription" className="setting_card">
                        <div className="icon_container">
                            <BsFillArchiveFill className="icon"/>
                        </div>
                        <span className="setting_title">Gi Mua</span>
                    </Link> : null}
                    
                    {this.state.perms.backup[0] == "1" ?
                    <Link to="admin/subscription" className="setting_card">
                        <div className="icon_container">
                            <BsFillArchiveFill className="icon"/>
                        </div>
                        <span className="setting_title">Qun L Gi Mua</span>
                    </Link> : null}

                    {this.state.perms.transaction_management[0] == "1" ?
                    <Link to="settings/transactions" className="setting_card">
                        <div className="icon_container">
                            <Ionicons.IoJournalOutline className="icon"/>
                        </div>
                        <span className="setting_title">Qun L Giao Dch</span>
                    </Link> : null}
                    
                    {/* {this.state.perms.backup[0] == "1" ?
                    <Link to="backup" className="setting_card">
                        <div className="icon_container">
                            <BsFillArchiveFill className="icon"/>
                        </div>
                        <span className="setting_title">Sao Lu</span>
                    </Link> : null} */}
                </div>
            </div>
        )
    }
}

export default SettingScreen;