import React, {Component} from 'react';
import {DefaultHeaderBar} from '@components/Header_bar';
import {Link} from 'react-router-dom';

import {BsPeople, BsInboxes, BsCreditCard, BsPersonCheck, BsFolderCheck} from 'react-icons/bs';

import API from '@api';

export default class PersonnelScreen extends Component {
    constructor(props) {
        super(props);
        this.state = {
            perms: null
        } 

        API.Permission.getUserPerm().then(res => {
            this.setState({
                perms: res.data.perms
            })
        }).catch(e => console.log(e));
    }

    render() {
        if(!this.state.perms) {
            return(<div></div>)
        }

        return(
            <div className="settings_view">
                {localStorage.getItem('chuc_vu') ? <DefaultHeaderBar onGoBack={() => this.props.history.goBack()} title="Nhn S"/> : null}
                <div className="settings_container">
                    {this.state.perms.staff[0] == "1" ? 
                    <Link to="personnel/staff" className="setting_card">
                        <div className="icon_container">
                            <BsPeople className="icon"/>
                        </div>
                        <span className="setting_title">Nhn Vin</span>
                    </Link> : null}
                    
                    {this.state.perms.staff_salary[0] == "1" ?
                    <Link to="personnel/staff_salary" className="setting_card">
                        <div className="icon_container">
                            <BsCreditCard className="icon"/>
                        </div>
                        <span className="setting_title">Lng Nhn Vin</span>
                    </Link> : null}
                    
                    <Link to="personnel/cham_cong" className="setting_card">
                        <div className="icon_container">
                            <BsPersonCheck className="icon"/>
                        </div>
                        <span className="setting_title">Chm Cng</span>
                    </Link>
                    
                    {this.state.perms.permissions[0] == "1" ? 
                    <Link to="personnel/permission" className="setting_card">
                        <div className="icon_container">
                            <BsInboxes className="icon"/>
                        </div>
                        <span className="setting_title">Chc Nng Truy Cp</span>
                    </Link> : null}
                    
                    {this.state.perms.cham_cong[0] == "1" ? 
                    <Link to="personnel/cham_cong_list" className="setting_card">
                        <div className="icon_container">
                            <BsFolderCheck className="icon"/>
                        </div>
                        <span className="setting_title">Qun L Chm Cng</span>
                    </Link> : null}
                </div>
            </div>
        )
    }
}