import { NotificationManager } from "react-notifications";
import CONFIG from "@config";

const ERRS = CONFIG.HTTP_ERRORS;

class Alert {
    static _successTimeOut = 2000;

    static success(message) {
        NotificationManager.success(message, "Thnh cng", this._successTimeOut);
    }

    static error(message) {
        NotificationManager.error(ERRS[message] || message, "Li", this._successTimeOut);
    }

    static info(title, message, onClick=()=>{}) {
        NotificationManager.info(message, title, this._successTimeOut, onClick);
    }

    static warn(message, onClick=()=>{}) {
        NotificationManager.warning(message, "Cnh bo", this._successTimeOut, onClick);
    }
}

export default Alert;