import React, {Component} from 'react';
import {HashSpiner} from '@components/Spiners';
import * as Icons from 'react-icons/io5';

import "@styles/components/Modals.scss";

export class FormModal extends Component {
    render() {
        if(this.props.loading) {
            return(
                <div className="modal">
                    <div className="modal__close-btn" onClick={this.props.onClose}>
                        <Icons.IoClose className="modal__close-btn__icon"/>
                    </div>
                     <div className="form_container">
                        <HashSpiner/>
                        <a>LOADING...</a>
                     </div>
                </div>
            )
        }

        return(
            <div onClick={this.props.onClose} className="modal" style={{display: `${this.props.is_open === "true" || this.props.is_open == true ? "flex" : "none"}`}}>
                <div className="modal__close-btn" onClick={this.props.onClose}>
                    <Icons.IoClose className="modal__close-btn__icon"/>
                </div>
                <div onClick={e => e.stopPropagation()} className="form_container">
                     <h1>{this.props.title}</h1>
                     {this.props.children}

                     <div className="column">
                            {
                                this.props.onRemove ?
                                <button className="btn" onClick={this.props.onRemove}>Xo</button> : null
                            }
                            {
                                this.props.onClose && !this.props.onRemove ?
                                <button className="btn" onClick={this.props.onClose}>Hy</button> : null
                            }
                            {
                                this.props.onSave ?
                                <button className="btn blue" onClick={this.props.onSave}>Lu</button> : null   
                            }
                     </div>
                </div>
            </div>
        )
    }
}