class DefaultHeaderBar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            sidebar_visible: false,
            filter_dropdown_visible: false,
            branch: null,
            searching: false
        }

        API.Branch.getAll().then(res => {
            this.setState({
                branch: res.data
            })
        });
    }

    //SHOW FILTER DROPDOWN
    handleShowFilterDropdown = () => {
        setTimeout(() => {
            this.setState({
                filter_dropdown_visible: true
            })
        }, 100)
    }

    //HIDE FILTER DROPDOWN
    handleHideFilterDropdown = () => {
        this.setState({
            filter_dropdown_visible: false
        })
    }

    render() {
        if(!this.state.branch) {
            return <div></div>
        }

        if(this.state.searching) {
            return <FullSearchBox autocomplete_data={this.props.autocomplete_data} onKeyUp={(e) => this.props.onSearchKeyPress(e.target.value)} onClose={() => {this.setState({searching: false})}}/>
        }

        return(
            <div className="header_bar">
                {isMobile ? (this.props.back_link !== undefined ? <Link onClick={this.props.onGoBack} className="side_bar_btn"><RiArrowLeftSLine className="icon"/></Link> : <button className="side_bar_btn" onClick={() => this.setState({sidebar_visible: true})}><AiOutlineMenu className="icon"/></button>) : null}
                {this.props.onGoBack !== undefined ? 
                <h1 className="back" onClick={this.props.onGoBack}><RiArrowLeftSLine className="icon"/>{this.props.title}</h1> : 
                    <h1>{this.props.title}</h1>
                }
                
                <div className="right_tools_container">
                    <div className="right_tools_box">
                        <h4 className="custom_title" style={{opacity: .55, fontSize: 13, fontWeight: 'bold', marginRight: 12}}>{this.props.customTitle}</h4>
                        {
                            this.props.actions?.map((item, index) => {
                                return (
                                    <div key={`header-action-item-${index}`}>
                                        { item }
                                    </div>
                                );
                            })
                        }
                        {
                            this.props.onCustomerFilter &&
                            <div className="dropdown_group">
                                {isMobile ? <button onClick={this.handleShowFilterDropdown}><RiFilter3Line className="icon"/></button> : <button onClick={this.handleShowFilterDropdown}><RiFilter3Line className="icon"/>Lc</button>} 
                                <CustomerFilterDropDown visible={this.state.filter_dropdown_visible} onFilter={this.props.onCustomerFilter} onHideDropDown={this.handleHideFilterDropdown}/> 
                            </div>
                        }
                        {this.props.handleSalaryFilter !== undefined ?  
                        <div className="dropdown_group">
                            {isMobile ? <button onClick={this.handleShowFilterDropdown}><RiFilter3Line className="icon"/></button> : <button onClick={this.handleShowFilterDropdown}><RiFilter3Line className="icon"/>Lc</button>}
                            <FilterDropDown type="1" onHideDropDown={this.handleHideFilterDropdown} visible={this.state.filter_dropdown_visible} onFilter={this.props.onDropdownFilterSalaryBtnClick}/>
                        </div>:null}
                        {this.props.goHistory !== null && this.props.goHistory !== undefined? <Link className="link_btn" to={this.props.goHistory}><MdRestore className="icon"/>{isMobile ? null : "Lch s chnh sa"}</Link> : null}
                        {this.props.handleEdit !== null && this.props.handleEdit !== undefined ? <button onClick={this.props.handleEdit}><RiPencilLine className="icon"/>{isMobile ? null : "Chnh sa" }</button> : null}
                        {this.props.create_btn_link !== null && this.props.create_btn_link !== undefined? <Link className="link_btn" to={this.props.create_btn_link}><BsPlus className="icon"/>{isMobile ? null : "To mi"}</Link> : null}
                        {this.props.handleCreate !== null && this.props.handleCreate !== undefined? <button onClick={this.props.handleCreate}><BsPlus className="icon"/>{isMobile ? null : "To mi"}</button> : null}
                        {this.props.onDropdownFilterBtnClick !== undefined ? <div className="dropdown_group">
                            {isMobile ? <button onClick={this.handleShowFilterDropdown}><RiFilter3Line className="icon"/></button> : <button onClick={this.handleShowFilterDropdown}><RiFilter3Line className="icon"/>Lc</button>}
                            <FilterDropDown onHideDropDown={this.handleHideFilterDropdown} visible={this.state.filter_dropdown_visible} onFilter={this.props.onDropdownFilterBtnClick}/>
                        </div> : null }
                        {this.props.handleAddProduct !== undefined && this.props.handleAddProduct !== null ?<button onClick={this.props.handleAddProduct}><BsPlus className="icon"/>{isMobile ? null : "Thm sn phm"}</button> : null}
                        {this.props.handlePrintBill !== undefined ? <button onClick={this.props.handlePrintBill}><RiPrinterLine className="icon"/>{isMobile ? null : "In ha n"}</button> : null}
                        {this.props.handleSave !== null && this.props.handleSave !== undefined? <button onClick={this.props.handleSave}><RiSaveLine className="icon"/>{isMobile ? null : "Lu"}</button> : null}
                        {this.props.handleRestore !== null && this.props.handleRestore !== undefined ? <button onClick={this.props.handleRestore}><MdRestore className="icon"/>{isMobile? null : "Khi phc n hng"}</button> : null}
                        {this.props.handleRemove !== null && this.props.handleRemove !== undefined? <button onClick={this.props.handleRemove}><RiDeleteBinLine className="icon"/>{isMobile ? null : "Xa"}</button> : null}
                        {this.props.handleCalendarFilter !== undefined ?
                            <select ref="branch" onChange={this.props.handleCalendarFilter}>
                                <option value="" disabled selected>Chi nhnh</option>
                                <option value="all">Tt c</option>
                                {this.state.branch.map((data, i) => {
                                    return(
                                        <option value={data.ten_chi_nhanh}>{data.ten_chi_nhanh}</option>
                                    )
                                })}
                            </select>: null}
                            {this.props.onSearchKeyPress !== undefined && isMobile ?
                            <button onClick={() => {this.setState({searching: true})}}>
                                <RiSearchLine className="icon"/>
                            </button> : null }
                        {this.props.onSearchKeyPress !== undefined ?                           
                            <div className="search_box">
                                <RiSearchLine className="icon"/>
                                <input type="text" ref="search" placeholder="Tm kim" onKeyUp={(e) => this.props.onSearchKeyPress(e.target.value)}></input>
                            
                                {this.props.autocomplete_data != undefined ? 
                                    <div className="autocomplete_container">
                                        <ul>
                                            {this.props.autocomplete_data.map((data, i) => {
                                                console.log(data);
                                                return(<li>
                                                    <Link to={`bill_edit/${data._id}`}>
                                                        {data.title}
                                                        <br/><br/>
                                                        Ngy: {data.ngay_chup}
                                                        <br/> 
                                                        Gi chp: {data.gio_chup}
                                                    </Link></li>)
                                            })}
                                        </ul>
                                    </div> : null}

                            </div>       : null}
                    </div>
                </div>
            </div>
        )
    }
}

export {
    HeaderBar,
    BillEditHeader,
    SettingsHeader,
    DefaultHeaderBar
}