import Axios from '@api/Axios';

const PackageAPI = {
    add: async(data) => await Axios.post('packages', data),
    get: async(id) => await Axios.get(`packages/${id}`),
    getType: async(type) => await Axios.get(`packages/type/${type}`),
    getAll: async() => await Axios.get(`packages`),
    update: async(id, data) => await Axios.put(`packages/${id}`, data),
    remove: async(id) =>  await Axios.delete(`packages/${id}`),
}

export default PackageAPI;