import React, {Component} from 'react';
import {Redirect} from 'react-router-dom';

class Logout extends Component {
    constructor(props) {
        super(props);

        this.state = { 
            success: false
        }
    
        this.handleLogout();
    }
    
    componentWillUnmount = () => {
        window.location.reload();
    }

    handleLogout = () => {
        localStorage.clear();

        this.setState({
            success: true
        });

        window.location.reload();
    }

    render() {
        if(this.state.success) {
            return(
                <Redirect to='/login'/>
            )
        }

        return(
            <div></div>
        )
    }
}

export default Logout;