import socketIOClient from 'socket.io-client';
import config from '@config';

import BillSocket from './Bill';
import CompanySocket from './Company';
import SpendingSocket from './Spending';

class Socket {
    socket;
    companyId;
    userId;

    constructor() {
        this.socket = socketIOClient(config.IO_URL);

        //Join company
        this.companyId = localStorage.getItem('company_id');
        let accessToken = localStorage.getItem("JWT");
        this.socket.emit('/company/join', {
            access_token: accessToken
        });
    }

    Bill() {
        return new BillSocket(this.socket, this.companyId);
    }

    Company() {
        return new CompanySocket(this.socket);
    }

    Spending() {
        return new SpendingSocket(this.socket, this.companyId);
    }
}

export default Socket;