class SpendingSocket {
    socket;
    companyId;

    constructor(socket, companyId) {
        this.socket = socket;
        this.companyId = companyId;
    }
    
    //*EMITS
    emitAdd(data) {
        return this.socket.emit(`/client/spendings/add`, {
            ...data,
            company_id: this.companyId
        });
    }
    
    emitUpdate(data) {
        return this.socket.emit('/client/spendings/update', {
            ...data,
            company_id: this.companyId
        });
    }
    
    emitRemove(id) {
        return this.socket.emit(`/client/spendings/remove`, {
            id,
            company_id: this.companyId
        });
    }

    //*LISTENERS
    onAdd(callback) {
        this.socket.on(`/server/spendings/add`, callback);
    }

    onUpdate(callback) {
        this.socket.on(`/server/spendings/update`, callback);
    }

    onRemove(callback) {
        this.socket.on(`/server/spendings/remove`, callback);
    }
}

export default SpendingSocket;