import React, {Component} from 'react';
import "@styles/components/Calendar.scss";
import {DefaultHeaderBar} from '@components/Header_bar';
import {Redirect} from 'react-router-dom';
import {LavenCalendar} from '@components/Calendar/Calendar';
import {HashSpiner} from '@components/Spiners';
import {CalendarModal, MobileCalendarModal} from '@components/Modals';
import {isMobile} from 'react-device-detect';
import {removeVietnameseTones, findWithAttr} from '@functions/Global';
import {LINK} from '@functions/Global';
import Socket from '@socket';

import API from '@api';

const socket = new Socket();

class CalendarScreen extends Component {
    constructor(props) {
        super(props);
        this.state = {
            events: [],
            redirect: false,
            link: null,
            branch: null,
            old_events: [],
            modal_visible: false,
            modal_data: null,
            m_modal_visible: false,
            m_modal_data: null,
            modal_date: '',
            autocomplete_data: null
        }

        this.fetchData();
    }

    async fetchData() {
        let branchSelected = JSON.parse(localStorage.getItem("branch_selected") || "[]");

        await API.Bill.getAll().then(res => {
            let events = [];

            let data = res.data.filter((data, i) => {
                //Filter by date
                if((parseInt((data.ngay_chup.split("-")[1])) - 1) !== (new Date().getMonth())) return false;
                if((parseInt((data.ngay_chup.split("-")[2]))) !== (new Date().getFullYear())) return false;

                //Filter by branch
                if(branchSelected.indexOf(data.chi_nhanh._id) <= -1) return false;

                return !data.deleted;
            });

            data.forEach((data) => {
                const gioi_tinh = data.gioi_tinh_be === "0" ? "B trai" : "B gi"//new Date(data.ngay_chup + ' ' + data.gio_chup)
            
                let ngay_chup = data.ngay_chup
                ngay_chup = ngay_chup ? ngay_chup.split("-")[0].length == 2 ? ngay_chup : ngay_chup.split("-")[2] + '-' + ngay_chup.split("-")[1] + '-' + ngay_chup.split("-")[0] : null

                events.push({
                    title: `${data.gio_chup} ${data.chi_nhanh?.ten_chi_nhanh} - ${data.products[0] === undefined ? "Khng c gi chp" : data.products[0].ten_goi} - ${data.ten_khach_hang} - ${data.so_dien_thoai} - ${gioi_tinh}`, 
                    ngay_chup: `${ngay_chup}`,
                    gio_chup: `${data.gio_chup}`,
                    tinh_trang: data.tinh_trang,
                    branch: data.chi_nhanh?.ten_chi_nhanh,
                    color: data.chi_nhanh.color,
                    _id: data._id
                })
            });

            this.setState({
                events: events,
                old_events: events
            })
        }).catch(e => console.log(e));

        await API.Branch.getAll().then(res => {
            this.setState({
                branch: res.data
            })
        }).catch(e => console.log(e));

        await API.Status.getAll().then(res => {
            this.setState({
                status: res.data
            })
        }).catch(e => console.log(e));

        //Load last filter
        let billFilter = JSON.parse(localStorage.getItem("bill_filter")) || {
            photo: "all",
            photoshop: "all",
            support: "all",
            tinh_trang: "all",
            date: new Date().getFullYear() + '-' + ((new Date().getMonth() + 1) <= 9 ? '0' + (new Date().getMonth() + 1) : (new Date().getMonth() + 1)),
            year: new Date().getFullYear(),
            month: ((new Date().getMonth() + 1) <= 9 ? '0' + (new Date().getMonth() + 1) : (new Date().getMonth() + 1))
        };

        this.handleFilter(
            billFilter.photo,
            billFilter.support,
            billFilter.tinh_trang,
            billFilter.month,
            billFilter.year,
            billFilter.photoshop
        )
    }

    componentDidMount = () => {
        //LISTEN SERVER UPDATE BILL
        socket.Bill().onUpdate(data => {
            let chi_nhanh = findWithAttr(this.state.branch,'_id', data.chi_nhanh);
            chi_nhanh = this.state.branch[chi_nhanh];
            const gioi_tinh = data.gioi_tinh_be === "0" ? "B trai" : "B gi"//new Date(data.ngay_chup + ' ' + data.gio_chup)
            let ngay_chup = data.ngay_chup
            ngay_chup = ngay_chup ? ngay_chup.split("-")[0].length == 2 ? ngay_chup : ngay_chup.split("-")[2] + '-' + ngay_chup.split("-")[1] + '-' + ngay_chup.split("-")[0] : null
            let events = this.state.events;
            let event_data = {
                title: `${data.gio_chup} ${chi_nhanh?.ten_chi_nhanh} - ${data.products[0] === undefined ? "Khng c gi chp" : data.products[0].ten_goi} - ${data.ten_khach_hang} - ${data.so_dien_thoai} - ${gioi_tinh}`, 
                ngay_chup: `${ngay_chup}`,
                gio_chup: `${data.gio_chup}`,
                tinh_trang: data.tinh_trang,
                branch: chi_nhanh?.ten_chi_nhanh,
                color: chi_nhanh.color,
                _id: data._id
            }
            
            events[findWithAttr(events, "_id", data._id)] = event_data;

            this.setState({
                events: events
            })
        })

        //LISTEN SERVER CREATE BILL
        socket.Bill().onAdd(data => {
            let chi_nhanh = findWithAttr(this.state.branch,'_id', data.chi_nhanh);
            chi_nhanh = this.state.branch[chi_nhanh];
            const gioi_tinh = data.gioi_tinh_be === "0" ? "B trai" : "B gi"//new Date(data.ngay_chup + ' ' + data.gio_chup)
            let ngay_chup = data.ngay_chup
            ngay_chup = ngay_chup ? ngay_chup.split("-")[0].length == 2 ? ngay_chup : ngay_chup.split("-")[2] + '-' + ngay_chup.split("-")[1] + '-' + ngay_chup.split("-")[0] : null
            
            let events = this.state.events;
            let event_data = {
                title: `${data.gio_chup} ${chi_nhanh?.ten_chi_nhanh} - ${data.products[0] === undefined ? "Khng c gi chp" : data.products[0].ten_goi} - ${data.ten_khach_hang} - ${data.so_dien_thoai} - ${gioi_tinh}`, 
                ngay_chup: `${ngay_chup}`,
                gio_chup: `${data.gio_chup}`,
                tinh_trang: data.tinh_trang,
                branch: chi_nhanh?.ten_chi_nhanh,
                color: chi_nhanh.color,
                _id: data._id
            }
            
            events.push(event_data);

            this.setState({
                events: events
            })
        })

        //LISTEN SERVER REMOVE BILL
        socket.Bill().onRemove(id => {
            let events = this.state.events;
            events.splice(findWithAttr(events, "_id", id), 1);

            this.setState({
                events: events
            })
        })
    }

    //XU LY CLICK
    handleEventClick = (e) => {
        this.setState({
            modal_visible: true,
            modal_data: e,
            modal_date: e
        })
    }
    
    //THC HIN LC T DROPDOWN
    handleFilter = (photo, support, tinh_trang, month, year, photoshop) => {
        let events = [];
        let branchSelected = JSON.parse(localStorage.getItem("branch_selected") || "[]");

        //RELOAD DATA
        API.Bill.getAll().then(res => {
            //LC DATA
            let filtered_data = res.data.filter((data, i) => {
                //LC THEO CHI NHNH
                if(branchSelected.indexOf(data.chi_nhanh._id) <= -1) return false;

                //LC THEO PHOTO
                if(photo !== "all") {
                    if(data.photo !== photo) return false;
                }

                //LC THEO PHOTOSHOP
                if(photoshop !== "all") {
                    if(data.photoshop !== photoshop) return false;
                }

                //LC THEO SUPPORT
                if(support !== "all") {
                    if(data.support !== support) return false;
                }

                //LC THEO TNH TRNG
                if(tinh_trang !== "all") {
                    if(data.tinh_trang._id !== tinh_trang) return false;
                }

                if(!data.deleted) {
                    const gioi_tinh = data.gioi_tinh_be === "0" ? "B trai" : "B gi"//new Date(data.ngay_chup + ' ' + data.gio_chup)
                
                    let ngay_chup = data.ngay_chup
                    ngay_chup = ngay_chup ? ngay_chup.split("-")[0].length == 2 ? ngay_chup : ngay_chup.split("-")[2] + '-' + ngay_chup.split("-")[1] + '-' + ngay_chup.split("-")[0] : null

                    events.push({
                        title: `${data.gio_chup} ${data.chi_nhanh?.ten_chi_nhanh} - ${data.products[0] === undefined ? "Khng c gi chp" : data.products[0].ten_goi} - ${data.ten_khach_hang} - ${data.so_dien_thoai} - ${gioi_tinh}`, 
                        ngay_chup: `${ngay_chup}`,
                        gio_chup: `${data.gio_chup}`,
                        tinh_trang: data.tinh_trang,
                        branch: data.chi_nhanh?.ten_chi_nhanh,
                        color: data.chi_nhanh.color,
                        _id: data._id
                    })

                    return true;
                }

                return false;
            });
  
            this.setState({
                events: events,
                old_events: events,
                bill: filtered_data,
                default_bill_data: filtered_data
            })
        }).catch(e => console.log(e));
    }

    //THC HIN SEARCH
    handleSearch = (search_value) => {
        //RELOAD DATA IF SEARCH VALUE IS NULL
        if(search_value === "") {
            API.Bill.getAll().then((res) => {
                let data = res.data.filter((data) => {
                    return !data.deleted;
                })

                this.setState({
                    bill: data
                })            
            })
        }
        //RELOAD DATA
        API.Bill.getAll().then((res) => {
            let data = res.data.filter((d) => {
                return !d.deleted;
            })

            let events = [];

            //TM KHCH HNG OR TN B OR SS IN THOI
            let filtered_data = data.filter((data, i) => {
                if(removeVietnameseTones(data.ten_khach_hang).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true; 
                if(removeVietnameseTones(data.so_dien_thoai).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true; 
                
                return false;
            })

            filtered_data.forEach((data) => {
                const gioi_tinh = data.gioi_tinh_be === "0" ? "B trai" : "B gi"//new Date(data.ngay_chup + ' ' + data.gio_chup)
                
                events.push({
                    title: `${data.chi_nhanh?.ten_chi_nhanh} - ${data.products[0] === undefined ? "Khng c gi chp" : data.products[0].ten_goi} - ${data.ten_khach_hang} - ${data.so_dien_thoai} - ${gioi_tinh}`, 
                    ngay_chup: `${data.ngay_chup}`,
                    gio_chup: `${data.gio_chup}`,
                    tinh_trang: data.tinh_trang,
                    branch: data.chi_nhanh?.ten_chi_nhanh,
                    _id: data._id
                })
            });

            this.setState({
                autocomplete_data: events
            })           
        }).catch(e => console.log(e));
    }

    render() {
        if(!this.state.status) {
            return <div></div>
        }

        if(this.state.redirect) {
            return(
                <Redirect to={`bill_edit/${this.state.link}`}/>
            )
        }

        return(
            <div>
                <CalendarModal events={this.state.events} date={this.state.modal_date} status={this.state.status} data={this.state.modal_data} visible={this.state.modal_visible} handleCloseModal={() => this.setState({modal_visible: false})}/>
                <DefaultHeaderBar 
                    // onSearchKeyPress={this.handleSearch} 
                    // autocomplete_data= {this.state.autocomplete_data} 
                    onDropdownFilterBtnClick={this.handleFilter} 
                    create_btn_link="bill_edit/create_new/"
                    title="Lch"
                />
                {isMobile ? <MobileCalendarModal visible={this.state.m_modal_visible} data={this.state.m_modal_data} handleCloseModal={() => this.setState({m_modal_visible: false})} branch={this.state.branch}/> : null}
                <div className="view">
                    <LavenCalendar events={this.state.events} branch={this.state.branch} onClick={this.handleEventClick} onClickDate={(e) => this.setState({m_modal_data: e, m_modal_visible: true})}/>
                </div>
            </div>
        )
    }
}

export default CalendarScreen;