import React, {Component} from 'react';
import { StickyTable, Row, Cell } from 'react-sticky-table';
import './styles.scss';

export class PackageTable extends Component {
    handleEdit = (id) => {
        this.props.handleOpenModal(id);
    }

    render() {
        return(
            <div className="table_view">
              <div className="table">
                <StickyTable>
                  <Row className="header">
                    <Cell style={{width: "32px"}}>STT</Cell>
                    <Cell>Tn Gi</Cell>
                    <Cell>Gi Tin</Cell>
                    <Cell>Nhp Hng</Cell>
                    <Cell>Xut</Cell>
                    <Cell>Tn</Cell>
                    <Cell>Thng Tin Chi Tit</Cell>
                  </Row>
                  {this.props.data.map((data, i) => {
                    return(
                      <Row className="row" onClick={() => this.handleEdit(data._id)}>
                        <Cell>{i + 1}</Cell>
                        <Cell>{data.ten_goi}</Cell>
                        <Cell>{data.gia}</Cell>
                        <Cell>{data.soLuong}</Cell>
                        <Cell>{data.soLuongXuat}</Cell>
                        <Cell>{data.tonKho}</Cell>
                        <Cell>{data.description}</Cell>
                      </Row>
                    )
                  })}
                </StickyTable>
              </div>
            </div>
        )
    }
}