import HTTP_ERRORS from "./httpErrors";

const DEBUG = false;
const IO_URL = DEBUG ? 'http://localhost:9006' : 'https://server.stumanage.lavenes.com';
const API_URL = DEBUG ? 'http://localhost:9006' : 'https://server.stumanage.lavenes.com';
const API_VER = '/api/v1';

const CONFIG = {
    DEBUG,
    IO_URL,
    API_URL,
    API_VER,
    API: `${API_URL}${API_VER}`,
    HEADERS: {
        'Content-Type': 'application/json',
        'Accept': 'application/json',
    },
    COLORS: {
        "blue": "#3880ff",
        "green": "#33b127",
        "orange": "#ffa500",
        "yellow": "#ffc409",
        "red": "#eb445a",
        "black": "#414141",
        "gray": "#888"
    },
    APP_VERSION: "4.9.3 ",
    HTTP_ERRORS: HTTP_ERRORS
}

export const generateApiHeader = () => {
    return {
        ...CONFIG.HEADERS,
        user_id: localStorage.getItem('user_id') || "",
        company_id: localStorage.getItem('company_id') || "",
        Authorization: localStorage.getItem('JWT') ? `Bearer ${localStorage.getItem('JWT')}` : "",
    }
}

export default CONFIG;