import CONFIG, {generateApiHeader} from "@config";
import axios from 'axios';
import Alert from '@utils/Alert';

class Axios {
    static async post(url, data, headers) {
        const response = await axios.post(`${CONFIG.API}/${url}`, data, {
            headers: {
                ...headers,
                ...generateApiHeader()
            }
        });

        if(response.data.code !== 200) {
            Alert.error(response.data.message);
            throw new Error(response.data.message);
        }

        Alert.success("Thm thnh cng!");

        return response.data;
    }

    static async get(url, headers) {
        const response = await axios.get(`${CONFIG.API}/${url}`, {
            headers: {
                ...headers,
                ...generateApiHeader()
            }
        });

        if(response.data.code !== 200) {
            Alert.error(response.data.message);
            this.checkRedirect(response.data.message);

            throw new Error(response.data.message);
        }

        return response.data;
    }

    static async put(url, data, headers) {
        const response = await axios.put(`${CONFIG.API}/${url}`, data, {
            headers: {
                ...headers,
                ...generateApiHeader()
            }
        });

        if(response.data.code !== 200) {
            Alert.error(response.data.message);
            throw new Error(response.data.message);
        }

        Alert.success("Cp nht thnh cng!");

        return response.data;
    }

    static async delete(url, headers) {
        const response = await axios.delete(`${CONFIG.API}/${url}`, {
            headers: {
                ...headers,
                ...generateApiHeader()
            }
        });

        if(response.data.code !== 200) {
            Alert.error(response.data.message);
            throw new Error(response.data.message);
        }

        Alert.success("Xo thnh cng!");

        return response.data;
    }

    static async checkRedirect(errorMsg) {
        let urlHash = window.location.hash;
        let originHash = `#/${urlHash.split('/')[1]}`;

        switch(errorMsg) {
            case "FEATURE_NOT_AVAILABLE" : {
                originHash = `${originHash}/errors/feature_not_available`;
            }
            break;
            default:
                break;
        }

        window.location.hash = originHash;
    }
}

export default Axios;