import React, {Component} from 'react';
import {DefaultHeaderBar} from '@components/Header_bar';
import { Table, FormModal } from '@components';
import CONFIG from '@config';
import API from '@api';

const TABLE_COLUMNS = [
    {
        key: "ten_tinh_trang",
        title: "Tn tnh trng",
        colored: true
    }
]

class StatusScreen extends Component {
    constructor(props) {
        super(props);

        this.state = {
            is_modal_open: "false",
            data: null,
            edit_id: null,
            perms: null,
            color: CONFIG.COLORS["blue"],
        }

        API.Status.getAll().then(res => {
            this.setState({
                data: res.data
            });
        }).catch(e => console.log(e));

        API.Permission.getUserPerm().then(res => {
            this.setState({
                perms: res.data.perms
            });
        }).catch(e => console.log(e));
    }

    handleOpenCreateModal = () => {
        this.refs.ten_tinh_trang.value = "";

        this.setState ({
            is_modal_open: "true",
            color: CONFIG.COLORS["blue"],
        })
    }

    handleOpenEditModal = (id) => {
        API.Status.get(id).then(res => {
            this.refs.ten_tinh_trang.value = res.data.ten_tinh_trang;

            this.setState({
                is_modal_open: "true",
                edit_id: id,
                color: res.data.color,
            });
        }).catch(e => console.log(e));
    }

    updateData = () => {
        this.refs.ten_tinh_trang.value = "";
        
        API.Status.getAll().then(res => {
            this.setState({
                data: res.data,
                is_modal_open: "false",
                edit_id: null,
                color: CONFIG.COLORS["blue"],
            });
        }).catch(e => console.log(e));
    }

    handleChangeColor = (color) => {
        this.setState({
            color: color
        })
    }

    handleSave = async() => {
        let data = {
            ten_tinh_trang: this.refs.ten_tinh_trang.value,
            color: this.state.color
        }

        if(this.state.edit_id) {//*UPDATE
            await API.Status.update(this.state.edit_id, data).catch(e => console.log(e));
        }else{//*CREATE
            await API.Status.add(data).catch(e => console.log(e));
        }

        this.updateData();
    }

    handleRemove = () => {
        API.Status.remove(this.state.edit_id).then(res => {
            this.updateData();
        }).catch(e => console.log(e));
    }

    render() {
        if(!this.state.data || !this.state.perms) {
            return(<div></div>);
        }
        return(
            <div>
                <DefaultHeaderBar  
                    onGoBack={() => this.props.history.goBack()} 
                    title="Tnh Trng"  
                    handleCreate={this.state.perms.status[1] == "1" ? this.handleOpenCreateModal : null}
                />
                
                <Table
                    columns={TABLE_COLUMNS}
                    data={this.state.data}
                    onClick={this.handleOpenEditModal}
                />

                <FormModal 
                    title="Thm Tnh Trng"
                    is_open={this.state.is_modal_open} 
                    onClose={
                        !this.state.edit_id ? 
                        () => this.setState({is_modal_open: "false"}) : null
                    }
                    onSave={
                        this.state.perms.status[2] == "1" ?
                        () => this.handleSave() : null
                    }
                    onRemove={
                        this.state.perms.status[3] == "1" && this.state.edit_id ?
                        () => this.handleRemove() : null
                    }
                >
                    <div className="box">
                        <span className="title">Tn tnh trng</span>
                        <input type="text" className="textbox" ref="ten_tinh_trang"/>
                    </div>

                    <div className="box">
                        <span className="title">Chn mu</span>
                        <div className="colors_picker_container">
                            {
                                Object.values(CONFIG.COLORS).map((color, i) => {
                                    return (
                                        <div 
                                            className={this.state.color === color ? "active color" : "color"} 
                                            key={`color-select-${i}`}
                                            style={{
                                                backgroundColor: color,
                                            }}
                                            onClick={() => this.handleChangeColor(color)}>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                </FormModal> 
            </div>
        )
    }
}

export default StatusScreen;