import Axios from '@api/Axios';

const PermissionAPI = {
    add: async(ten_nhom) => await Axios.post('permissions', {ten_nhom}),
    get: async(id) => await Axios.get(`permissions/${id}`),
    getUserPerm: async() => await Axios.get(`permissions/${localStorage.getItem('chuc_vu')}`),
    getAll: async() => await Axios.get(`permissions`),
    update: async(id, data) => await Axios.put(`permissions/${id}`, data),
    remove: async(id) =>  await Axios.delete(`permissions/${id}`),
}

export default PermissionAPI;