import React, {Component} from 'react';
import {PackageHeader} from '@components';
import {removeVietnameseTones} from '@functions/Global';
import {HashSpiner} from '@components/Spiners';
import { PackageTable, FormModal } from "@components";
import API from '@api';

class PackageScreen extends Component {
    constructor(props) {
        super(props);
        this.state =  {}

        this.fetchData();
    }

    fetchData = () => {
        this.setState({
            modal_is_open: "false",
            edit_modal_is_open: "false",
            data_goi_chup: null,
            goi_chup_type_id: null,
            loading: false,
            edit_id: null,
            perms: null
        })

        API.PackageType.getSlug(this.props.match.params.type).then(package_type => {
            API.Package.getType(package_type.data._id).then(res => {
                let data = res.data;

                data = data.sort((a, b) => {
                    return a.ten_goi.localeCompare(b.ten_goi);
                });

                this.setState ({
                    goi_chup_type_id: package_type.data._id,
                    data_goi_chup: data
                })
            }).catch(e => console.log(e));
        }).catch(e => console.log(e));

        API.Permission.getUserPerm().then(res => {
            this.setState({
                perms: res.data.perms
            })
        }).catch(e => console.log(e));
    }

    componentDidUpdate(prevProps) {
        if (this.props.location !== prevProps.location) {
            this.fetchData();
        }
    }

    handleOpenModal = () => {
        this.refs.ten_goi.value = "";
        this.refs.gia.value = "";

        this.setState({
            modal_is_open: "true"
        })
    }

    handleOpenEditModal = (id) => {
        API.Package.get(id).then(res => {
            let data = res.data;

            this.refs.ten_goi.value = data.ten_goi;
            this.refs.gia.value = data.gia;
            this.refs.description.value = data.description || "";
            this.refs.soLuong.value = data.soLuong;

            this.setState({
                modal_is_open: "true",
                edit_id: id
            })
        }).catch(e => console.log(e));
    }

    //THC HIN SEARCH
    handleSearch = (search_value) => {
        //RELOAD DATA IF SEARCH VALUE IS NULL
        if(search_value === "") {
            API.PackageType.getSlug(this.props.match.params.type).then(package_type => {
                API.Package.getType(package_type.data._id).then(res => {
                    this.setState ({
                        data_goi_chup: res.data
                    })
                })
            });
        }

        //RELOAD DATA
        API.PackageType.getSlug(this.props.match.params.type).then(package_type => {
            API.Package.getType(package_type.data._id).then(res => {
                let data = res.data.reverse()
        
                //TM KHCH HNG OR TN B OR SS IN THOI
                let filtered_data = data.filter((data, i) => {
                    if(removeVietnameseTones(data.ten_goi).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true;  
                    
                    return false;
                })
        
                this.setState({
                    data_goi_chup: filtered_data
                })  
            })
        });      
    }

    updateData = () => {
        API.Package.getType(this.state.goi_chup_type_id).then(res => {
            this.setState ({
              data_goi_chup: res.data,
              modal_is_open: "false",
              edit_modal_is_open: "false",
              loading: false
            })
        }).catch(e => console.log(e));
    }

    handleSave = () => {
        let data = {
            ten_goi: this.refs.ten_goi.value,
            gia: this.refs.gia.value,
            type: this.state.goi_chup_type_id,
            description: this.refs.description.value,
            soLuong: this.refs.soLuong.value
        }

        if(this.state.edit_id) { //Update
            API.Package.update(this.state.edit_id, data).then(res => {
                this.updateData();
            }).catch(e => console.log(e));
        }else{ //Create
            API.Package.add(data).then(res => {
                this.updateData();
            }).catch(e => console.log(e));
        }
    }

    handleRemove = () => {
        API.Package.remove(this.state.edit_id).then(res => {
            this.updateData();
        }).catch(e => console.log(e));
    }

    render() {
        if(!this.state.data_goi_chup || !this.state.perms) {
            return(<div className="load_view"><HashSpiner/></div>)
        }
        return(
            <div>
                <PackageHeader handleOpenModal={this.handleOpenModal} onSearchKeyPress={this.handleSearch}/>
                <PackageTable data={this.state.data_goi_chup} ref="table" handleOpenModal={this.handleOpenEditModal}/>
                
                <FormModal 
                    title="Thm Gi Chp"
                    is_open={this.state.modal_is_open} 
                    onClose={
                        () => this.setState({modal_is_open: "false"})
                    }
                    onSave={
                        this.state.perms.goi_chup[2] == "1" ?
                        () => this.handleSave() : null
                    }
                    onRemove={
                        this.state.perms.goi_chup[3] == "1" && this.state.edit_id ?
                        () => this.handleRemove() : null
                    }
                >
                     <div className="box">
                         <span className="title">Tn gi</span>
                         <input type="text" className="textbox" ref="ten_goi"/>
                     </div>
                     <div className="box">
                         <span className="title">S lng</span>
                         <input type="text" className="textbox" ref="soLuong"/>
                     </div>
                     <div className="box">
                         <span className="title">Gi</span>
                         <input type="text" className="textbox" ref="gia"/>
                     </div>
                     <div className="box">
                         <span className="title">Thng tin chi tit</span>
                         <textarea rows={6} className="textbox" ref="description"></textarea>
                     </div>
                </FormModal>  
            </div>
        )
    }
}

export default PackageScreen;