import React, { useState, useEffect, useRef } from 'react';
import { DefaultHeaderBar } from '@components/Header_bar';
import { PaperCard, FormModal } from '@components';
import API from '@api';
import DateProto from '@utils/Date';
import { salaryCalculate } from '@functions/Global';

import "./styles.scss";

export const ProfileView = ({ history }) => {
    const [user, setUser] = useState({});
    const [salary, setSalary] = useState({});
    const [salaryTable, setSalaryTable] = useState({});
    const [password, setPassword] = useState('');
    const [modalVisible, setModalVisible] = useState(false);
    const oldPasswordRef = useRef(0);
    const passwordRef = useRef(0);

    useEffect(() => {
        fetchData();
    }, []);

    // useEffect(() => {
    //     const delayDebounceFn = setTimeout(async () => {
    //         if(password) {
    //             //!FIXME: PASSWORD IS NULL WHEN UPDATE
    //             await API.Staff.update(user._id, {
    //                 password
    //             })
    //         }
    //     }, 1000)
    
    //     return () => clearTimeout(delayDebounceFn)
    // }, [password])

    const fetchData = async () => {
        let userId = localStorage.getItem("user_id");
        let user = await API.Staff.get(userId);
        let salary = await API.StaffSalary.getUser(userId);

        setUser(user.data);
        setSalary(salary.data);

        salaryCalculate(user.data, new Date().getFullYear(), new Date().getMonth(), e => {
            setSalaryTable(e);
        })
    }

    const handleChangePassword = async () => {
        let oldPassword = oldPasswordRef.current.value;
        let password = passwordRef.current.value;

        await API.Staff.updatePassword(oldPassword, password).catch(e => alert("Mt khu khng ng!"));

        setModalVisible(false);
    }

    return (
        <div className="view profile-view">
            <DefaultHeaderBar  
                onGoBack={() => history.goBack()} 
                title="Thng Tin C Nhn"
            />  

            <div className="profile-view__container">
                <PaperCard title="Thng tin c nhn">
                    <span><b>H tn: </b><span>{ user.ho_ten }</span></span>
                    <span><b>Ngy sinh: </b><span>{ new Date(user.ngay_sinh).format("dd/MM/yyyy") } - { new Date(user.ngay_sinh).getAge() } tui</span></span>
                    <span><b>S in thoi: </b><span>{ user.so_dien_thoai }</span></span>
                    <span><b>TK Ngn Hng: </b><span>{ user.banking_address }</span></span>
                    <span><b>a ch v: </b><span>{ user.wallet_address }</span></span>
                    <span><b>Ngy vo lm: </b><span>{ new Date(user.date_created).format("dd/MM/yyyy") }</span></span>
                    <span><b>Mt khu: </b></span>
                    <button className="btn blue" onClick={() => setModalVisible(true)}>i mt khu</button>
                </PaperCard>
                <PaperCard title="Bng lng">
                    <span><b>Lng c bn: </b><span>{ salary.luong_co_ban }</span></span>
                    <span><b>Ngy cng: </b><span>{ salary.ngay_cong }</span></span>
                    <span><b>Lng thng ny: </b><span>{ salaryTable.luong_thang }</span></span>
                    <span><b>% thng t vn (GC): </b><span>{ salary.phan_tram_thuong_goi_chup_tu_van } %</span></span>
                    <span><b>% thng photo: </b><span>{ salary.phan_tram_thuong_goi_chup_photo } %</span></span>
                    <span><b>% thng photoshop: </b><span>{ salary.phan_tram_thuong_photoshop } %</span></span>
                    <span><b>% thng t vn (DV): </b><span>{ salary.phan_tram_thuong_dich_vu_tu_van } %</span></span>
                    <span><b>Thng dch v: </b><span>{ salaryTable.total_service }</span></span>
                    <span><b>Tng thng doanh thu: </b><span>{ salaryTable.total_photo }</span></span>
                    <span><b>Tng thng: </b><span>{ salaryTable.total }</span></span>
                    <hr/>
                    <span><b>Thc lnh: </b><span>{ salaryTable.thuc_lanh }</span></span>
                </PaperCard>
            </div>

            <FormModal 
                title="i Mt Khu"
                is_open={modalVisible} 
                onClose={
                    () => setModalVisible(false)
                }
                onSave={handleChangePassword}
            >
                <div className="box">
                    <span className="title">Mt khu c</span>
                    <input type="password" className="textbox" ref={oldPasswordRef}/>
                </div>
                <div className="box">
                    <span className="title">Mt khu mi</span>
                    <input type="password" className="textbox" ref={passwordRef}/>
                </div>
            </FormModal>  
        </div>
    )
}