import React, { Component, useEffect, useState } from 'react';
import { Switch, Route } from 'react-router-dom';
import CONFIG from '@config';
import { SideBar } from "@components";
import { NotificationContainer } from 'react-notifications';
import 'react-notifications/lib/notifications.css';

//VIEWS
import Home from './views/Home';
import Products from './views/Products';
import Logout from './views/Logout';
import {Redirect} from 'react-router-dom';
import SalaryEdit from './views/SalaryEdit';
import Backup from './views/Backup';

//*Generic
import PackageScreen from '@views/Package';
import { BillEditScreen, BillPrintScreen, BillHistoryScreen } from '@views/Bill';
import CalendarScreen from '@views/Calendar';
import CustomerScreen from '@views/Customer';
import SpendingScreen from '@views/Spending';
import { TotalAnalyticScreen, RevenueScreen } from '@views/Analytics';

//*Auth Screens
import SignupScreen from '@views/Signup';
import LoginScreen from '@views/Login';

//*Settings
import SettingScreen from '@views/Settings';
import PermissionScreen from '@views/Settings/Permission';
import BranchScreen from '@views/Settings/Branch';
import { StaffScreen, StaffEditScreen, StaffSalaryScreen } from '@views/Settings/Staff';
import TrashScreen from '@views/Settings/Trash';
import PersonnelScreen from '@views/Settings/Personnel';
import { ChamCongScreen, ChamCongListScreen, ChamCongManagementScreen } from '@views/Settings/ChamCong';
import StatusScreen from '@views/Settings/Status';
import SubscriptionScreen from '@views/Settings/Subscription';
import { TransactionManage } from '@views/Settings/TransactionManage';
import { ProfileView } from '@views/Settings/Profile';

//*Transactions
import { CloseTransactionScreen, TransactionSuccessScreen } from '@views/Transactions';

//*Errors
import { FeatureNotAvaiErrorScreen } from '@views/Errors';

//*Admin
import { AdminSubscriptionScreen } from '@views/Settings/Admin/Subscription';

import API from "@api";
import Socket from "@socket";
import Alert from '@utils/Alert';

const socket = new Socket();

const PrivateRoute = ({ component: Component, ...rest }) => (
  <Route {...rest} render={(props) => (
    localStorage.getItem('authed')
      ? <Component {...props} />
      : <Redirect to='/login' />
  )} />
)

const App = () => {
  const [auth] = useState(true);

  useEffect(() => {
    //Check time loggedin to Cham Cong
    API.User.verifyLoggedTime();

    //Check Auth
    API.User.verifyToken();

    //Cham cong
    if(localStorage.getItem('authed')) {
      API.User.chamCong();
    }

    initSocket();
  }, []);

  const initSocket = async() => {
    socket.Spending().onAdd(data => {
      Alert.info("C giao dch mi!", "C giao dch mi!", () => {
        window.location.href=`/#/${localStorage.getItem("company_slug")}/settings/transactions`
      });
    })
  }

  return (
    <div>
      {localStorage.getItem('authed') ? <SideBar/> : null}
      {auth ? <Main/> : null}
      <Version/>
      <NotificationContainer/>
    </div>
  );
}

const Version = () => {
  return(
    <div className="version">
      <a href="https://www.lavenes.com/" target="_blank">Beta {CONFIG.APP_VERSION}</a>
    </div>
  )
}

const Main = (props) => {
  return (
    <main>
      <Switch>
        {/* GLOBAL */}
        <Route exact path='/signup' component={SignupScreen}/>
        <Route exact path='/login' component={LoginScreen}/>
        <Route exact path='/backup' component={Backup}/>
        <Route exact path='/backup/:type' component={Backup}/>

        <Route exact path='/transaction/close' component={CloseTransactionScreen}/>
        <Route exact path='/transaction/successful' component={TransactionSuccessScreen}/>

        {/* ADMIN */}
        <Route exact path='/:company_slug/admin/subscription' component={AdminSubscriptionScreen}/>

        {/* USER */}
        <Route exact path='/:company_slug/errors/feature_not_available' component={FeatureNotAvaiErrorScreen}/>
        <PrivateRoute exact path='/:company_slug/' component={Home}/>
        <PrivateRoute exact path='/:company_slug/settings/transactions' component={TransactionManage}/>
        <PrivateRoute exact path='/:company_slug/settings/profile' component={ProfileView}/>
        <PrivateRoute exact path='/:company_slug/settings/personnel/cham_cong' component={ChamCongScreen}/>
        <PrivateRoute exact path='/:company_slug/settings/personnel/cham_cong_list' component={ChamCongListScreen}/>
        <PrivateRoute exact path='/:company_slug/settings/personnel/cham_cong_edit/:id' component={ChamCongManagementScreen}/>
        <PrivateRoute exact path='/:company_slug/spending' component={SpendingScreen}/>
        <PrivateRoute exact path='/:company_slug/bill_history/:id' component={BillHistoryScreen}/>
        <PrivateRoute exact path='/:company_slug/bill_edit/:id' component={BillEditScreen}/>
        <PrivateRoute exact path='/:company_slug/bill_edit/:id/:date' component={BillEditScreen}/>
        <PrivateRoute exact path='/:company_slug/bill_restore/:id' component={BillEditScreen}/>
        <PrivateRoute exact path='/:company_slug/bill_print/:id' component={BillPrintScreen}/>
        <PrivateRoute exact path='/:company_slug/calendar' component={CalendarScreen}/>
        <PrivateRoute exact path='/:company_slug/customers' component={CustomerScreen}/>
        <PrivateRoute exact path='/:company_slug/packages/:type' component={PackageScreen}/>
        <PrivateRoute exact path='/:company_slug/products' component={Products}/>
        <PrivateRoute exact path='/:company_slug/total_revenue' component={TotalAnalyticScreen}/>
        <PrivateRoute exact path='/:company_slug/realtime_revenue' component={RevenueScreen}/>
        <PrivateRoute exact path='/:company_slug/trash' component={TrashScreen}/>
        <PrivateRoute exact path='/:company_slug/settings' component={SettingScreen}/>
        <PrivateRoute exact path='/:company_slug/subscription' component={SubscriptionScreen}/>
        <PrivateRoute exact path='/:company_slug/settings/personnel' component={PersonnelScreen}/>
        <PrivateRoute exact path='/:company_slug/settings/personnel/staff' component={StaffScreen}/>
        <PrivateRoute exact path='/:company_slug/settings/personnel/staff_edit/:id' component={StaffEditScreen}/>
        <PrivateRoute exact path='/:company_slug/settings/personnel/permission' component={PermissionScreen}/>
        <PrivateRoute exact path='/:company_slug/branch' component={BranchScreen}/>
        <PrivateRoute exact path='/:company_slug/status' component={StatusScreen}/>
        <PrivateRoute exact path='/:company_slug/settings/personnel/staff_salary' component={StaffSalaryScreen}/>
        <PrivateRoute exact path='/:company_slug/settings/personnel/staff_salary_edit/:id' component={SalaryEdit}/>
        <PrivateRoute exact path='/:company_slug/logout' component={Logout}/>
        <Route exact path='/' component={LoginScreen}/>
      </Switch>
    </main>
  )
}

export default App;