import React, {Component} from 'react';
import {HeaderBar} from '../components/Header_bar';
import {BillTableContainer} from '../components/Table';
import {removeVietnameseTones, transDate} from '../functions/Global';
import Excel from '@utils/Excel';
import API from '@api';

const WAIT_INTERVAL = 1000;

class Home extends Component {
    constructor(props) {
        super(props);

        this.state = {
            bill: null,
            default_bill_data: null,
            typing: false,
            typingTimeout: 0
        }

        this.handleFilter = this.handleFilter.bind(this);

        this.fetchData();
    }

    async fetchData() {
        let branchSelected = JSON.parse(localStorage.getItem("branch_selected") || "[]");

        await API.Bill.getAll().then(res => {
            let data = res.data;

            data = data.filter((data, i) => {
                //Filter by date
                if((parseInt((data.ngay_chup.split("-")[1])) - 1) !== (new Date().getMonth())) return false;
                if((parseInt((data.ngay_chup.split("-")[2]))) !== (new Date().getFullYear())) return false;

                //Filter by branch
                if(branchSelected.indexOf(data.chi_nhanh._id) <= -1) return false;

                return !data.deleted;
            });
            
            this.setState ({
              bill: data,
              default_bill_data: data
            })
        }).catch(e => console.log(e));

        //Load last filter
        let billFilter = JSON.parse(localStorage.getItem("bill_filter")) || {
            photo: "all",
            photoshop: "all",
            support: "all",
            tinh_trang: "all",
            date: new Date().getFullYear() + '-' + ((new Date().getMonth() + 1) <= 9 ? '0' + (new Date().getMonth() + 1) : (new Date().getMonth() + 1)),
            year: new Date().getFullYear(),
            month: ((new Date().getMonth() + 1) <= 9 ? '0' + (new Date().getMonth() + 1) : (new Date().getMonth() + 1))
        };

        this.handleFilter(
            billFilter.photo,
            billFilter.support,
            billFilter.tinh_trang,
            billFilter.month,
            billFilter.year,
            billFilter.photoshop
        )
    }
    
    //THC HIN LC T DROPDOWN
    handleFilter(photo, support, tinh_trang, month, year, photoshop) {
        let branchSelected = JSON.parse(localStorage.getItem("branch_selected") || "[]");

        //RELOAD DATA
        API.Bill.getAll().then(res => {
            //LC DATA
            let filtered_data = res.data.filter((data, i) => {
                //LC THEO CHI NHNH
                if(branchSelected.indexOf(data.chi_nhanh._id) <= -1) return false;

                //LC THEO PHOTO
                if(photo !== "all") {
                    if(data.photo !== photo) return false;
                }


                //LC THEO SUPPORT
                if(support !== "all") {
                    if(data.support !== support) return false;
                }

                //LC THEO PHOTOSHOP
                if(photoshop !== "all") {
                if(data.photoshop !== photoshop) return false;
                }

                //LC THEO TNH TRNG
                if(tinh_trang !== "all") {
                    if(data.tinh_trang._id !== tinh_trang) return false;
                }

                //LC THEO THNG
                if(month !== "all") {
                    if((parseInt((data.ngay_chup.split("-")[1]))) !== parseInt(month)) return false;
                }

                //LC THEO NM
                if(year !== "all") {
                if((new Date(transDate.toOld(data.ngay_chup)).getFullYear()).toString() !== year.toString()) return false;
                }

                if(data.deleted) {
                    return false;
                }

                return true;
            });
  
            this.setState({
                bill: filtered_data,
                default_bill_data: filtered_data
            })
        }).catch(e => console.log(e));
    }

    //THC HIN SEARCH
    handleSearch = (search_value) => {
        if(this.state.typingTimeout) {
            clearTimeout(this.state.typingTimeout);
        }

        const self = this;

        //I NGI DNG NHP HT T MI LOAD
        this.setState({
            typing: false,
            typingTimeout: setTimeout(function () {
                self.search(search_value)
            }, 500)
        })
    }

    search = (search_value) => {
        let branchSelected = JSON.parse(localStorage.getItem("branch_selected") || "[]");

        API.Bill.getAll().then(res => {
            let data = res.data.sort((a, b) => { return new Date(transDate.toOld(b.ngay_chup)) - new Date(transDate.toOld(a.ngay_chup)) })

            data = data.filter((data, i) => {
                //Filter by date
                if((parseInt((data.ngay_chup.split("-")[1])) - 1) !== (new Date().getMonth())) return false;
                if((parseInt((data.ngay_chup.split("-")[2]))) !== (new Date().getFullYear())) return false;

                //Filter by branch
                if(branchSelected.indexOf(data.chi_nhanh._id) <= -1) return false;

                return !data.deleted;
            });

            //RELOAD DATA IF SEARCH VALUE IS NULL
            if(search_value === "") {
                  this.setState ({
                    bill: data
                  })
            }
    
            //TM KHCH HNG OR TN B OR SS IN THOI
            let filtered_data = data.filter((data, i) => {
                if(data.deleted) return false;
                if(removeVietnameseTones(data.ten_khach_hang).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true; 
                if(removeVietnameseTones(data.ho_ten_be).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true; 
                if(removeVietnameseTones(data.so_dien_thoai).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true; 
                return false;
            })
      
            this.setState({
                bill: filtered_data
            })
        }, this).catch(e => console.log(e));
    }

    handleDownloadBills = async () => {
        //*Convert to Excel File
        let rowsData = this.state.bill.map((item, index) => ([
            index + 1,
            item?.chi_nhanh.ten_chi_nhanh,
            item?.gio_chup,
            item?.ngay_chup,
            item?.ten_khach_hang,
            item?.so_dien_thoai,
            item?.ho_ten_be,
            item?.tong,
            0, //!TODO: Calculate tong with Dat Coc & Da Thu
            item?.con_no,
            item.tinh_trang.ten_tinh_trang,
            item.ngay_giao_hinh,
            item.ghi_chu
        ]));
        let excelFile = Excel.createExcel([
            "STT",
            "Chi nhnh",
            "Gi chp",
            "Ngy chp",
            "Tn khch hng",
            "ST",
            "H tn b",
            "Tng",
            " thu",
            "Cn n",
            "Tnh trng",
            "Ngy giao",
            "Ghi ch"
        ], rowsData);

        // Convert and Download file
        let dateNow = new Date();
        let filename = `DS_Don_Hang_${dateNow.getDate()}-${dateNow.getMonth() + 1}-${dateNow.getFullYear()}`;
        var blob = new Blob([excelFile], {
            type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
        });
        var element = document.createElement('a');
        element.setAttribute('href', window.URL.createObjectURL(blob));
        element.setAttribute('download', filename);
      
        element.style.display = 'none';
        document.body.appendChild(element);
      
        element.click();
      
        document.body.removeChild(element);
    }

    render() {
        if(!this.state.bill && !this.state.default_bill_data) {
            return(
                <div></div>
            )
        }

        return(
            <div>
                <HeaderBar 
                    onDownload={this.handleDownloadBills} 
                    title="n Hng" 
                    onDropdownFilterBtnClick={this.handleFilter} 
                    onSearchKeyPress={this.handleSearch}
                />
                <BillTableContainer data={this.state.bill} type="0"/>
            </div>
        )
    }
}

export default Home;