import React,{Component} from 'react';
import {DefaultHeaderBar} from '@components/Header_bar';
import {StaffTable} from '@components/Table';
import {removeVietnameseTones} from '@functions/Global';
import {HashSpiner} from '@components/Spiners';
import API from '@api';

import StaffEditScreen from './Edit';
import StaffSalaryScreen from './Salary';

export {
    StaffEditScreen,
    StaffSalaryScreen
};

export class StaffScreen extends Component {
    constructor(props) {
        super(props);
        this.state = {
            perms: null,
            data: null
        } 

        API.Permission.getUserPerm().then(res => {
            this.setState({
                perms: res.data.perms
            })
        }).catch(e => console.log(e));

        API.Staff.getAll().then(res => {
            this.setState({
                data: res.data
            })
        }).catch(e => console.log(e));
    }

    //THC HIN SEARCH
    handleSearch = (search_value) => {
        //RELOAD DATA IF SEARCH VALUE IS NULL
        if(search_value === "") {
            API.Staff.getAll().then(res => {
                this.setState({
                    data: res.data
                })
            }).catch(e => console.log(e));
        }

        //RELOAD DATA
        API.Staff.getAll().then(res => {
            let data = res.data;
  
            //TM KHCH HNG OR TN B OR SS IN THOI
            let filtered_data = data.filter((data, i) => {
                if(removeVietnameseTones(data.email).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true;  
                if(removeVietnameseTones(data.ho_ten).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true;  
                
                return false;
            })
  
            this.setState({
                data: filtered_data
            })   
        }).catch(e => console.log(e));
    }

    render() {
        if(!this.state.perms || !this.state.data) {
            return(<div className="load_view"><HashSpiner/></div>)
        }
        
        return(
            <div>
                <DefaultHeaderBar 
                    onGoBack={() => this.props.history.goBack()} 
                    title="Nhn Vin" 
                    onSearchKeyPress={this.handleSearch} 
                    handleSave={null} 
                    handleRemove={null} 
                    handleCreate={null} 
                    create_btn_link={this.state.perms.staff[1] == "1" ? "staff_edit/create_new" : null }/>
                <StaffTable data={this.state.data} perms={this.state.perms}/>
            </div>
        )
    }
}

export default StaffScreen;