import * as XLSX from 'xlsx';

class Excel {
    //*Create CSV
    static createCSV = (columns=[], data=[]) => {
        let csv = '';

        //Crete columns
        csv += columns.join(",");

        //Create rows
        data.forEach(item => {
            csv += `\n` + item.join(",");
        });

        return csv;
    }

    //*CSV to Excel Buffer
    static csvToBuffer = (csv) => {
        const arrayOfArrayCsv = csv.split("\n").map(row => {
            return row.split(",")
        });
        const wb = XLSX.utils.book_new();
        const newWs = XLSX.utils.aoa_to_sheet(arrayOfArrayCsv);
        XLSX.utils.book_append_sheet(wb, newWs);
        const rawExcel = XLSX.write(wb, { type: 'base64' })
        return rawExcel
    }

    //*Create Excel
    static createExcel = (columns=[], data=[]) => {
        //* Convert to CSV;
        let csv = Excel.createCSV(columns, data);
        let excelBuffer = Excel.csvToBuffer(csv);

        return Buffer.from(excelBuffer, 'base64');
    }
}

export default Excel;