import React, { useEffect, useState, useRef } from 'react';
import API from '@api';
import { Link, Redirect } from 'react-router-dom';

const LoginScreen = () => {
    const emailRef = useRef(null);
    const passwordRef = useRef(null);
    const [loginMsg, setLoginMsg] = useState(null);
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [companySlug, setCompanySlug] = useState('');

    useEffect(() => {
        if (localStorage.getItem('JWT')) {
            API.User.verifyToken().then(res => {
                setCompanySlug(res.data.company_slug);
                setIsLoggedIn(true);
            }).catch(e => console.log(e));
        }
    }, []);

    const _handleLogin = async() => {
        let email = emailRef.current.value;
        let password = passwordRef.current.value;

        let res = await API.User.auth(email, password).catch(e => console.log(e));

        if (res.code === 200) {
            //*Save storage
            localStorage.setItem('JWT', res.data.accessToken);
            await API.User.verifyToken().then(res => {
                setCompanySlug(res.data.company_slug);

                localStorage.setItem("company_slug", res.data.company_slug);
            }).catch(e => console.log(e));

            //Set default branch seelcted
            let branches = await API.Branch.getAll();
            branches = branches.data.map(b => b._id);

            localStorage.setItem("branch_selected", JSON.stringify(branches));
                
            window.location.reload();
        }else if(res.code === 400) {
            setLoginMsg("Sai email hoc mt khu ng nhp!");
        }else{
            setLoginMsg("C li xy ra trong qu trnh ng nhp");
        }
    }
    
    if(isLoggedIn) {
        return <Redirect to={`/${companySlug}/`}/>
    }

    return(
        <div style={{display: 'flex', flexDirection: 'column', width: '100%', height: '100vh', alignItems: 'center', justifyContent: 'center'}}>
            <h1>ng Nhp</h1>
            {
                loginMsg ?
                <a style={{marginBottom: "16px", fontSize: "14px", color: "red"}}>{loginMsg}</a> : null
            }
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                gap: 16
            }}>
                <input type="email" ref={emailRef} placeholder="Email" className="textbox"/>
                <input type="password" ref={passwordRef}  placeholder="Mt khu" className="textbox"/>
                <button className="btn blue" onClick={_handleLogin}>ng Nhp</button>

                <Link
                    to="/signup"
                    style={{
                        fontSize: 12,
                        color: "#2484ff",
                        textDecoration: "none",
                        marginTop: 5
                    }}
                >Bn cha c ti khon? To ti khon</Link>
            </div>
        </div>
    )
}

export default LoginScreen;