import React, { Component } from 'react';
import { DefaultHeaderBar } from '@components/Header_bar';
import { Table } from '@components';
import { removeVietnameseTones } from '@functions/Global';
import { HashSpiner } from '@components/Spiners';
import { Redirect } from 'react-router-dom';
import API from '@api';

const TABLE_COLUMNS = [
    {
        key: "ho_ten",
        title: "H tn",
    }
]

class ChamCongListScreen extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: null,
            redirectId: null
        }

        API.Staff.getAll().then(res => {
            this.setState({
                data: res.data
            })
        }).catch(e => console.log(e));
    }

    //THC HIN SEARCH
    handleSearch = (search_value) => {
        API.Staff.getAll().then(res => {
            let data = res.data.reverse();

            if(!search_value) {
                this.setState({
                    data: data
                })
            }
  
            //TM KHCH HNG OR TN B OR SS IN THOI
            let filtered_data = data.filter((data, i) => {
                if(removeVietnameseTones(data.email).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true;  
                if(removeVietnameseTones(data.ho_ten).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true;  
                
                return false;
            })
  
            this.setState({
                data: filtered_data
            })  
        }).catch(e => console.log(e));
    }

    render() {
        if(this.state.redirectId) {
            return <Redirect to={`cham_cong_edit/${this.state.redirectId}`} />
        }

        if(!this.state.data) {
            return(<div className="load_view"><HashSpiner/></div>)
        }

        return(
            <div>
                <DefaultHeaderBar handleEdit={null} onGoBack={() => this.props.history.goBack()} handleCreate={null} create_btn_link={null} onSearchKeyPress={this.handleSearch} handleSave={null} handleRemove={null} title="Qun L Chm Cng" back_link="/settings"/>
                <Table
                    columns={TABLE_COLUMNS}
                    data={this.state.data}
                    onClick={e => this.setState({
                        redirectId: e
                    })}
                />
            </div>
        )
    }
}

export default ChamCongListScreen;