import React,{Component} from 'react';
import {DefaultHeaderBar} from '@components/Header_bar';
import {StaffForm} from '@components';
import {Redirect} from 'react-router-dom';
import API from '@api';

class StaffEditScreen extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: null,
            perms: null
        }

        if(this.props.match.params.id !== "create_new") {
            API.Staff.get(this.props.match.params.id).then(res => {
                this.setState({
                    data: res.data
                })
            }).catch(e => console.log(e));
        }

        API.Permission.getUserPerm().then(res => {
            this.setState({
                perms: res.data.perms
            })
        }).catch(e => console.log(e));
    }

    handleSave = () => {
        this.refs.staff_form.handleSave();
    }

    saveStaff = (data) => {
        if(this.props.match.params.id !== "create_new") {
            let new_data = data;

            if(this.state.data.password === new_data.password) {
                delete new_data.password;
            }

            API.Staff.update(this.props.match.params.id, new_data).then(res => {
                this.props.history.goBack()
            }).catch(e => console.log(e));
        }else {
            API.Staff.add(data).then(res => {
                this.props.history.goBack()
            }).catch(e => console.log(e));
        }
    }

    handleRemove = () => {
        API.Staff.remove(this.props.match.params.id).then(res => {
            this.props.history.goBack();
        }).catch(e => console.log(e));
    }

    render() {
        if(!this.state.data && this.props.match.params.id !== "create_new" || !this.state.perms) {
            return(<div></div>)
        }

        return(
            <div className="view">
                {this.props.match.params.id !== "create_new" ? 
                    <DefaultHeaderBar 
                        create_btn_link={null}  
                        onGoBack={() => this.props.history.goBack()}  
                        handleRemove={this.state.perms.staff[3] == "1" ? this.handleRemove : null } 
                        title="Nhn Vin" 
                        handleSave={this.state.perms.staff[2] == "1" ? this.handleSave: null}/> : 
                    <DefaultHeaderBar  
                        onGoBack={() => this.props.history.goBack()}  
                        title="Nhn Vin" 
                        handleSave={this.handleSave}
                    />}
                <StaffForm ref="staff_form" data={this.state.data} onSave={this.saveStaff}/>
            </div>
        )
    }
}

export default StaffEditScreen;