import React, {Component} from 'react';
import { StickyTable, Row, Cell } from 'react-sticky-table';
import {numberWithCommas} from '@functions/Global';
import './styles.scss';

export class ProductListTable extends Component {
    render() {
        return(
            <div className="table_view bill_edit" style={{flex: 1}}>
              <div className="table">
                <StickyTable>
                  <Row className="header">
                    <Cell style={{width: "32px"}}>STT</Cell>
                    <Cell>Sn Phm</Cell>
                    <Cell>n Gi</Cell>
                    <Cell>S Lng</Cell>
                    <Cell>Gim Gi</Cell>
                    <Cell>Tng</Cell>
                  </Row>
                    {this.props.data.map((data, i) => {
                      return(
                        <Row className="row" onClick={() => this.props.onEdit(i)} title={data.description || ""}>
                          <Cell>{i + 1}</Cell>
                          <Cell>{data.ten_goi}</Cell>
                          <Cell>{numberWithCommas(data.gia)} </Cell>
                          <Cell>{data.so_luong}</Cell>
                          <Cell>{data.giam_gia ? numberWithCommas(data.giam_gia) : 0} </Cell>
                          <Cell>{numberWithCommas((parseInt(data.gia) * parseInt(data.so_luong) * ((100 - parseInt(data.chiet_khau))/100)) - (data.giam_gia ? parseInt(data.giam_gia) : 0))} </Cell>
                        </Row>
                      )
                    })}
                </StickyTable>
              </div>
            </div>
        )
    }
}