import Axios from '@api/Axios';

const StaffSalaryAPI = {
    add: async(data) => await Axios.post('salaries', data),
    get: async(id) => await Axios.get(`salaries/${id}`),
    getUser: async (id) => await Axios.get(`salaries/user/${id}`),
    getAll: async() => await Axios.get(`salaries`),
    update: async(id, data) => await Axios.put(`salaries/${id}`, data),
    remove: async(id) =>  await Axios.delete(`salaries/${id}`),
}

export default StaffSalaryAPI;