import Axios from '@api/Axios';

const ChamCongAPI = {
    add: async(data) => await Axios.post('cham_cong', data),
    get: async(id, month, year) => await Axios.get(`cham_cong/${id}/${month}/${year}`),
    getUser: async(month, year) => await Axios.get(`cham_cong/${localStorage.getItem('user_id')}/${month}/${year}`),
    chamCong: async userId => await Axios.post(`cham_cong/${userId}/today`),
    getAll: async() => await Axios.get(`cham_cong`),
    update: async(data) => await Axios.put(`cham_cong/${data.user_id}/${data.month}/${data.year}`, data),
    remove: async(id) =>  await Axios.delete(`cham_cong/${id}`),
}

export default ChamCongAPI;