import Axios from '@api/Axios';
import Socket from '@socket';

const socket = new Socket();

const SpendingAPI = {
    add: async(data) => {
        await Axios.post('spendings', data).then(res => {
            socket.Spending().emitAdd(data);

            return res.data;
        }).catch(e => {
            throw new Error(e);
        })
    },
    get: async(id) => await Axios.get(`spendings/${id}`),
    getAll: async() => await Axios.get(`spendings`),
    update: async(id, data) => await Axios.put(`spendings/${id}`, data),
    remove: async(id) =>  await Axios.delete(`spendings/${id}`),
    confirm: async(id, type) => await Axios.post(`spendings/confirm/${id}`, { type }),
}

export default SpendingAPI;