import React, {Component} from 'react';
import {numberWithCommas} from '@functions/Global';

import "@styles/components/Forms.scss";

export class ProductListForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dat_coc: this.props.data ? [...this.props.data.dat_coc] : [
                {
                    value: 0,
                    time: '',
                    des: '',
                    transaction: "2"
                }
            ],
            thanh_toan: this.props.data ? [...this.props.data.thanh_toan] : [
                {
                    value: 0,
                    time: '',
                    des: '',
                    transaction: "2"
                }
            ]
        }
    }

    handleSave = () => {
        let data = {
            tong: parseInt(this.refs.tong.value.replace(/,/g,"")),
            dat_coc: [...this.state.dat_coc],
            thanh_toan: [...this.state.thanh_toan],
            con_no: parseInt(this.refs.con_no_1.value.replace(/,/g,"")),
            giam_gia: 0
        }

        this.props.handleSavePayment(data);
    }

    handleChangeInput = (element, index, e) => {
        let data = [...this.state[element]]
        data[index].value = Number(e.target.value);

        let date_now = new Date().getFullYear()+'-'+(new Date().getMonth() + 1) +'-'+(new Date().getDate() <= 9 ? '0' + new Date().getDate() : new Date().getDate());
        let date_des = new Date();
        date_des = (date_des.getHours() <= 9 ? '0' + date_des.getHours() : date_des.getHours()) + ":" + (date_des.getMinutes() <= 9 ? '0' + date_des.getMinutes() : date_des.getMinutes()) + " " + (date_des.getDate() <= 9 ? '0' + date_des.getDate() : date_des.getDate()) + "/" + ((date_des.getMonth() + 1) <= 9 ? '0' + (date_des.getMonth() + 1) : (date_des.getMonth() + 1)) + "/" + date_des.getFullYear()

        data[index].time = date_now;
        data[index].des = date_des + ' - ' + localStorage.getItem("ho_ten");

        let paymentData = [
            ...data, 
            ...(element === "dat_coc" ? 
                this.state.thanh_toan : 
                this.state.dat_coc
            )
        ];

        let conNo = 0;

        paymentData.forEach(item => {
            conNo += item.value;
        });

        this.refs.con_no_1.value = numberWithCommas((parseInt(this.refs.tong.value.replace(/,/g,""))) - conNo)
    
        //Add new row if edit last child
        if(index + 1 == this.state.thanh_toan.length) {
            let extendThanhToan = [...this.state.thanh_toan];
            extendThanhToan.push({
                value: 0,
                time: '',
                des: '',
                transaction: "2"
            });

            this.setState({
                thanh_toan: extendThanhToan
            })
        }
    }

    //CHNH SA GI TR SAU KHI XA SN PHM
    handleUpdateConNo = (data) => {
        data["giam_gia"] = parseInt(data["giam_gia"] || 0);
        this.refs.con_no_1.value = numberWithCommas(parseInt(this.refs.con_no_1.value.replace(/,/g,"")) + (parseInt(data.gia) * parseInt(data.so_luong) - parseInt(data.giam_gia)));
    }

    //CHNH SA GI TR CN N SAU KHI THAY I SN PHM
    handleUpdateConNoAfterChangeProduct = (old_data, new_data) => {
        old_data["giam_gia"] = parseInt(old_data["giam_gia"] || "0");
        new_data["giam_gia"] = parseInt(new_data["giam_gia"] || "0");
        
        this.refs.con_no_1.value = numberWithCommas(parseInt(this.refs.con_no_1.value.replace(/,/g,"")) - (Math.abs(parseInt(old_data.gia) * parseInt(old_data.so_luong)) - parseInt(old_data.giam_gia)) + (parseInt(new_data.gia) * parseInt(new_data.so_luong)) - parseInt(new_data.giam_gia));
    }

    render() { 
        const datCoc = this.state.dat_coc;
        const thanhToan = this.state.thanh_toan;
        const conNo = this.props.data?.con_no || 0;

        return(
            <div className="product_list_form_container" style={{flex: 1}}>
                <div className="column">
                    <div className="box">
                        <span className="title">Tng:</span>
                        <input className="textbox" ref="tong" value={numberWithCommas(this.props.product_total)} type="text" disabled/>
                    </div>
                    {
                        datCoc.map((item, index) => {
                            return (
                                <div key={`dat-coc-form-${index}`} className="box">
                                    <span className="title">t Cc: {item.des}</span>
                                    <div className="douple_textbox">
                                        <input 
                                            className="textbox" 
                                            onKeyUp={(e) => this.handleChangeInput('dat_coc', index, e)} 
                                            defaultValue={item.value || 0} 
                                            type="number" 
                                            disabled={this.props.perms.bill[2] == "1" ? '' : 'disabled'}/>
                                        <select 
                                            className="selectbox" 
                                            defaultValue={item.transaction || "2"} 
                                            disabled={this.props.perms.bill[2] == "1" ? '' : 'disabled'}
                                            onChange={(e) => {
                                                let data = [...this.state.dat_coc];
                                                data[index].transaction = e.target.value;
                                                this.setState({dat_coc: data});
                                            }}
                                        >     
                                            <option disabled selected value="2"></option>
                                            <option value="0">Chuyn khon</option>
                                            <option value="1">Tin mt</option>
                                        </select>
                                    </div>
                                </div>
                            )
                        })
                    }
                    {
                        thanhToan.map((item, index) => {
                            return (
                                <div className="box" key={`thanh-toan-form-${index}`}>
                                    <span className="title">Thanh Ton L{index+1}: {item.desc}</span>
                                    <div className="douple_textbox">
                                        <input 
                                            className="textbox" 
                                            onKeyUp={(e) => this.handleChangeInput("thanh_toan", index, e)} 
                                            defaultValue={item.value || 0} 
                                            type="number" 
                                            disabled={this.props.perms.bill[2] == "1" ? '' : 'disabled'}/>
                                        <select 
                                            className="selectbox" 
                                            defaultValue={item.transaction || "2"} 
                                            disabled={this.props.perms.bill[2] == "1" ? '' : 'disabled'}
                                            onChange={(e) => {
                                                let data = [...this.state.thanh_toan];
                                                data[index].transaction = e.target.value;
                                                this.setState({thanh_toan: data});
                                            }}
                                        >
                                            <option disabled selected value="2"></option>
                                            <option value="0">Chuyn khon</option>
                                            <option value="1">Tin mt</option>
                                        </select>
                                    </div>
                                </div>
                            )
                        })
                    }
                    <div className="box">
                        <span className="title">Cn N:</span>
                        <div className="douple_textbox">
                            <input 
                                className="textbox" 
                                ref="con_no_1" 
                                onKeyUp={(e) => this.handleChangeInput("con_no_1", e)} 
                                defaultValue={numberWithCommas(conNo)} 
                                type="text" 
                                disabled
                            />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}