import Socket from '@socket';
import Axios from '@api/Axios';

const socket = new Socket();

const BillHistoryAPI = {
    restore: async(id) => await Axios.get(`bill_histories/restore/${id}`),
    getAll: async(id) => await Axios.get(`bill_histories/${id}`),
    get: async(billId, id) => await Axios.get(`bill_histories/${billId}/${id}`),
}

export default BillHistoryAPI;