import React, {Component} from 'react';
import { DefaultHeaderBar } from '@components/Header_bar';
import { Table, FormModal } from '@components';
import {removeVietnameseTones} from '@functions/Global';
import {HashSpiner} from '@components/Spiners';
import API from '@api';
import CONFIG from '@config';

const TABLE_COLUMNS = [
    {
        key: "ten_chi_nhanh",
        title: "Tn chi nhnh",
        colored: true,
    }, {
        key: "dia_chi",
        title: "a ch",
    }
]

class BranchScreen extends Component {
    constructor(props) {
        super(props);

        this.state = {
            is_modal_open: "false",
            data: null,
            edit_id: null,
            perms:null,
            color: CONFIG.COLORS["blue"],
        }

        API.Permission.getUserPerm().then(res => {
            this.setState({
                perms: res.data.perms
            });
        }).catch(e => console.log(e));

        API.Branch.getAll().then(res => {
            this.setState({
                data: res.data
            });
        }).catch(e => console.log(e));
    }

    handleOpenCreateModal = () => {
        this.refs.ten_chi_nhanh.value = "";
        this.refs.dia_chi.value = "";

        this.setState ({
            is_modal_open: "true",
            color: CONFIG.COLORS["blue"],
        })
    }

    handleOpenEditModal = (id, i) => {
        API.Branch.get(id).then(res => {
            let data = res.data;

            this.refs.ten_chi_nhanh.value = data.ten_chi_nhanh;
            this.refs.dia_chi.value = data.dia_chi;

            this.setState({
                is_modal_open: "true",
                edit_id: id,
                color: data.color,
            })
        }).catch(e => console.log(e));
    }

    //THC HIN SEARCH
    handleSearch = (search_value) => {
        //RELOAD DATA IF SEARCH VALUE IS NULL
        if(search_value === "") {
            API.Branch.getAll().then(res => {
                this.setState({
                    data: res.data.reverse()
                });
            });
        }

        //RELOAD DATA
        API.Branch.getAll((res) => {
            let data = res.data.reverse()
  
            //TM KHCH HNG OR TN B OR SS IN THOI
            let filtered_data = data.filter((data, i) => {
                if(removeVietnameseTones(data.ten_chi_nhanh).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true;  
                if(removeVietnameseTones(data.dia_chi).toLowerCase().indexOf(removeVietnameseTones(search_value).toLowerCase()) > -1) return true;  
                
                return false;
            })
  
            this.setState({
                data: filtered_data
            })            
        })
    }

    updateData = () => {
        API.Branch.getAll().then(res => {
            this.setState({
                data: res.data,
                is_modal_open: "false"
            });
        }).catch(e => console.log(e));
    }

    handleSave = () => {
        let data = {
            ten_chi_nhanh: this.refs.ten_chi_nhanh.value,
            dia_chi: this.refs.dia_chi.value,
            color: this.state.color
        }

        if(this.state.edit_id) { //*Update
            API.Branch.update(this.state.edit_id, data).then(res => {
                console.log(res);
                this.updateData();
            }).catch(e => console.log(e));
        }else{ //*Create
            API.Branch.add(data).then(res => {
                this.updateData();
            }).catch(e => console.log(e));
        }
    }

    handleRemove = () => {
        API.Branch.remove(this.state.edit_id).then(res => {
            this.updateData();
        }).catch(e => console.log(e));
    }

    handleChangeColor = (color) => {
        this.setState({
            color: color
        })
    }

    render() {
        if(!this.state.data || !this.state.perms) {
            return(<div className="load_view"><HashSpiner/></div>)
        }
        
        return(
            <div>
                <DefaultHeaderBar  
                    onGoBack={() => this.props.history.goBack()} 
                    title="Chi Nhnh" 
                    onSearchKeyPress={this.handleSearch} 
                    handleCreate={this.state.perms.branch[1] == "1" ? this.handleOpenCreateModal : null}
                />
                <Table
                    columns={TABLE_COLUMNS}
                    data={this.state.data}
                    onClick={this.handleOpenEditModal}
                />

                <FormModal 
                    title="Sa Chi Nhnh"
                    is_open={this.state.is_modal_open} 
                    onClose={
                        !this.state.edit_id ? 
                        () => this.setState({is_modal_open: "false"}) : null
                    }
                    onSave={
                        this.state.perms.branch[2] == "1" ?
                        () => this.handleSave() : null
                    }
                    onRemove={
                        this.state.perms.branch[3] == "1" && this.state.edit_id ?
                        () => this.handleRemove() : null
                    }
                >
                    <div className="box">
                        <span className="title">Tn chi nhnh</span>
                        <input type="text" className="textbox" ref="ten_chi_nhanh"/>
                    </div>

                    <div className="box textarea" style={{width: "234px"}}>
                        <span className="title">a ch</span>
                        <textarea className="textbox" ref="dia_chi"></textarea>
                    </div>

                    <div className="box">
                        <span className="title">Chn mu</span>
                        <div className="colors_picker_container">
                            {
                                Object.values(CONFIG.COLORS).map((color, i) => {
                                    return (
                                        <div 
                                            className={this.state.color === color ? "active color" : "color"} 
                                            key={`color-select-${i}`}
                                            style={{
                                                backgroundColor: color,
                                            }}
                                            onClick={() => this.handleChangeColor(color)}>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                </FormModal> 
            </div>
        )
    }
}

export default BranchScreen;