import CONFIG from "@config";
import axios from 'axios';

const CompanyAPI = {
    add: async (companyName, address, phone, name, email, password) => {
        const response = await axios.post(`${CONFIG.API}/companies`, {
            ten_cong_ty: companyName,
            dia_chi: address,
            so_dien_thoai: phone,
            ho_ten: name,
            email: email,
            password: password
        }, {
            headers: CONFIG.HEADERS
        });

        console.log(response.data);
        
        return response.data;
    },
}

export default CompanyAPI;