class PermissionScreen extends Component {

    constructor(props) {
        super(props);
        this.state = {
            is_modal_open: "false",
            is_edit_modal_open: "false",
            permission_data: null,
            tab_data: null,
            tab: 0,
            user_perms: null
        }     
        
        API.Permission.getAll().then(res => {
            this.setState({
                permission_data: res.data,
                tab_data: res.data[0].perms
            })
        }).catch(e => console.log(e));

        API.Permission.getUserPerm().then(res => {
            this.setState({
                user_perms: res.data.perms
            })
        }).catch(e => console.log(e));
    }

    onTabChange = (tab) => {   
        this.setState ({
            tab_data: null,
            tab: tab
        })     

        API.Permission.getAll().then((res) => {
            this.setState({
                tab_data: res.data[tab].perms
            })
        }).catch(e => console.log(e));
    }

    handleOpenCreateModal = () => {
        this.setState ({
            is_modal_open: "true"
        })
    }

    handleOpenEditModal = () => {
        this.setState ({
            is_edit_modal_open: "true"
        })
    }

    handleCloseModal = () => {
        this.setState ({
            is_modal_open: "false",
            is_edit_modal_open: "false"
        })
    }

    //UPDATE PERMISSION WHEN USER CHECK ON CHECKBOX
    handleTickBox = (event, element, location) => {
        let data = this.state.tab_data;
        let permission_data = this.state.permission_data;
        permission_data = permission_data[this.state.tab];
        permission_data["perms"] = data;

        let element_data = {}
        element_data[element] = ["0","0","0","0"];

        if(data[element]) {
            data[element][location] = event.target.checked ? "1" : "0";
        }else{ 
            element_data[element][location] = event.target.checked ? "1" : "0";
            data = Object.assign(data, element_data);
        }

        API.Permission.update(permission_data._id, permission_data).then(res => {}).catch(e => console.log(e));

        //updatePermission(permission_data, (res) => {});
    }

    //UPDATE DATA AFTER USER EDIT
    handleUpdate = () => {
        API.Permission.getAll().then(res => {
            this.setState({
                permission_data: res.data,
                tab_data: res.data[0].perms,
                tab: 0
            });

            this.handleCloseModal();
        }).catch(e => console.log(e));
    }

    render() {
        if(!this.state.permission_data || !this.state.tab_data || !this.state.user_perms) {
            return (
                <div className="view">
                    <DefaultHeaderBar  onGoBack={() => this.props.history.goBack()} title="Chc Nng Truy Cp" handleCreate={this.handleUpdateData}/>
                </div>
            )
        }
        return(
            <div className="view">
                <DefaultHeaderBar  
                    onGoBack={() => this.props.history.goBack()} 
                    handleCreate={null} 
                    create_btn_link={null} 
                    handleSave={null} 
                    handleRemove={null} 
                    title="Chc Nng Truy Cp" 
                    handleEdit={ this.state.user_perms.permissions[2] == "1" ? this.handleOpenEditModal : null} 
                    handleCreate={this.state.user_perms.permissions[1] == "1" ? this.handleOpenCreateModal :null}
                />
                <TabBar onChangeTab={this.onTabChange} tab={this.state.tab} data={this.state.permission_data}/>
                <TabContentContainer data={this.state.tab_data} tab={this.state.tab} onTickBox={this.handleTickBox}/>
                <PermissionModal 
                    is_open={this.state.is_modal_open} 
                    handleCloseModal={this.handleCloseModal} 
                    onCreateNew={this.handleUpdate}
                />
                <PermissionEditModal 
                    is_open={this.state.is_edit_modal_open} 
                    tab={this.state.tab} 
                    handleSave={this.handleUpdate} 
                    handleRemove={this.handleUpdate} 
                    ten_nhom={this.state.permission_data[this.state.tab].ten_nhom} 
                    _id={this.state.permission_data[this.state.tab]._id} 
                    handleCloseModal={this.handleCloseModal}
                />
            </div>
        )
    }
}

export default PermissionScreen;