import Axios from '@api/Axios';

const StatusAPI = {
    add: async(data) => await Axios.post('status', data),
    get: async(id) => await Axios.get(`status/${id}`),
    getAll: async() => await Axios.get(`status`),
    update: async(id, data) => await Axios.put(`status/${id}`, data),
    remove: async(id) =>  await Axios.delete(`status/${id}`),
}

export default StatusAPI;