import React from 'react';
import { BsCheck } from 'react-icons/bs';

import "./styles.scss";

export const SubscriptionCard = ({ title, description, price, strikePrice, save, onClick, subscribed }) => {
    return (
        <div className={`card card_subscription ${subscribed ? 'subscribed' : ''}`}>
            <div className="title-container">
                <span className="title">{ title }</span>
            </div>
            <div className="description-container">
                {
                    description?.map((item, index) => {
                        return(
                            <div className="description-section" key={`description-section-${index}`}>
                                <div className="icon-container">
                                    <BsCheck className="icon"/>
                                </div>
                                <p className="description">{ item }</p>
                            </div>
                        )
                    })
                }
            </div>
            <div className="price-container">
                <span className="price">{ price } </span>
                <span className="sub-title">Annually: Save { save } <span className="strike-price">{ strikePrice } </span></span>
            </div>
            <div className="button-container">
                <button className="button" onClick={onClick} disabled={subscribed}>
                    {
                        subscribed ?
                        " ng K" : "ng K"
                    }
                </button>
            </div>
        </div>
    )
}

export const PaperCard = ({ children, onClick, title }) => {
    return (
        <div className="card__paper" onClick={onClick}>
            <h1 className="card__paper__title">{ title }</h1>
            { children }
        </div>
    )
}