class BillSocket {
    socket;
    companyId;

    constructor(socket, companyId) {
        this.socket = socket;
        this.companyId = companyId;
    }
    
    //*EMITS
    emitAdd(data) {
        return this.socket.emit(`/client/bills/add`, {
            ...data,
            company_id: this.companyId
        });
    }
    
    emitUpdate(data) {
        return this.socket.emit('/client/bills/update', {
            ...data,
            company_id: this.companyId
        });
    }
    
    emitRemove(id) {
        return this.socket.emit(`/client/bills/remove`, {
            id,
            company_id: this.companyId
        });
    }

    //*LISTENERS
    onAdd(callback) {
        this.socket.on(`/server/bills/add`, callback);
    }

    onUpdate(callback) {
        this.socket.on(`/server/bills/update`, callback);
    }

    onRemove(callback) {
        this.socket.on(`/server/bills/remove`, callback);
    }
}

export default BillSocket;