import React, { useEffect, useState } from 'react';
import { DefaultHeaderBar } from '@components/Header_bar';
import { PaperCard, Button } from '@components';
import { useParams } from 'react-router-dom';
import Alert from '@utils/Alert';
import API from '@api';
import "./styles.scss";

const HistoryCode = {
    "add" : " thm mi",
    "update" : " chnh sa",
    "restore" : " khi phc"
};

const TimeLine = () => {
    return (
        <div className="timeline">
            <div className="timeline__dot"></div>
            <div className="timeline__date-container">
                <span className="timeline__date-container__value">Nov 3, 2022</span>
            </div>
        </div>
    )
}

export const BillHistoryScreen = ({ props }) => {
    let { id } = useParams();
    const [histories, setHistories] = useState([]);
    const [historySelected, setHistorySelected] = useState(0);
    const [users, setUsers] = useState([]);
    const [status, setStatus] = useState([]);
    const [branches, setBranches] = useState([]);

    useEffect(() => {
        fetchData();
    }, []);

    const fetchData = async() => {
        //Fetch histories
        let histories = await API.BillHistories.getAll(id);
        histories = histories.data.reverse();

        setHistories(histories);

        //Fetch user
        let users = await API.Staff.getAll();
        setUsers(users.data);

        //Fetch status
        let status = await API.Status.getAll();
        setStatus(status.data);

        //Fetch branhces
        let branches = await API.Branch.getAll();
        setBranches(branches.data);
    }

    const handleRestore = async () => {
        await API.BillHistories.restore(histories[historySelected]._id);
        Alert.success(" khi phc thnh cng!");

        //Reload data
        fetchData();
    }

    //*Extract value from key
    const extractValue = (key, id) => {
        const billMap = {
            user_id : {
                name: "Ngi to",
                data: users,
                findKey: "_id",
                valKey: "ho_ten"
            },
            giam_gia: {
                name: "Gim gi"
            },
            con_no: {
                name: "Cn n"
            },
            tong: {
                name: "Tng"
            },
            ngay_giao_hinh: {
                name: "Ngy giao hnh"
            },
            tinh_trang: {
                name: "Tnh trng",
                data: status,
                findKey: "_id",
                valKey: "ten_tinh_trang"
            },
            so_dien_thoai: {
                name: "S in thoi"
            },
            so_tuoi: {
                name: "S tui b"
            },
            gioi_tinh_be: {
                name: "Gii tnh b"
            },
            ngay_sinh_be: {
                name: "Ngy sinh b"
            },
            ho_ten_be: {
                name: "H tn b"
            },
            ten_khach_hang: {
                name: "Tn khch hng"
            },
            consultant : {
                name: "T vn",
                data: users,
                findKey: "_id",
                valKey: "ho_ten"
            },
            photoshop : {
                name: "Photoshop",
                data: users,
                findKey: "_id",
                valKey: "ho_ten"
            },
            support : {
                name: "H tr",
                data: users,
                findKey: "_id",
                valKey: "ho_ten"
            },
            photo : {
                name: "Photo",
                data: users,
                findKey: "_id",
                valKey: "ho_ten"
            },
            ghi_chu: {
                name: "Ghi ch"
            },
            ngay_chup: {
                name: "Ngy chp"
            },
            gio_chup: {
                name: "Gi chp"
            },
            chi_nhanh : {
                name: "Tn chi nhnh",
                data: branches,
                findKey: "_id",
                valKey: "ten_chi_nhanh"
            },
            products: {
                name: "Sn phm",
                valMap: {
                    chiet_khau: {
                        name: "Chit khu"
                    },
                    so_luong: {
                        name: "S lng"
                    },
                    ten_goi: {
                        name: "Tn gi"
                    },
                    giam_gia: {
                        name: "Gim gi"
                    },
                    gia: {
                        name: "Gi"
                    },
                }
            },
            thanh_toan: {
                name: "Thanh ton",
                valMap: {
                    value: {
                        name: "S tin"
                    },
                    transaction: {
                        name: "Loi thanh ton"
                    },
                },
            },
            dat_coc: {
                name: "t cc",
                valMap: {
                    value: {
                        name: "S tin"
                    },
                    transaction: {
                        name: "Loi thanh ton"
                    },
                },
            },
        }

        //Get name & value
        let mapVal = billMap[key];
        let name = mapVal?.name || key;
        let value = mapVal?.data?.find(i => i[mapVal.findKey])?.[mapVal.valKey] || id;
        let valMap = mapVal?.valMap || {};

        return {
            name,
            value,
            valMap
        }
    }

    return (
        <div className="view bill_history">
            <DefaultHeaderBar  
                onGoBack={() => props.history.goBack()} 
                title="n Hng" 
                back_link="/" 
            />  

            <div className="container">
                <div className="col">
                    <PaperCard>
                        <h1>Details</h1>
                        <span><b>Ngi chnh sa: </b><span>{histories[historySelected]?.author.ho_ten}</span></span>
                        <span><b>Thi gian: </b><span>{histories[historySelected]?.date_created}</span></span>
                        <hr/>
                        {
                            Object.keys(histories[historySelected]?.data || {}).map((item, index) => {
                                if(item == "_id" || item == "deleted" || item == "date_created") return;

                                let key = item;
                                let value = histories[historySelected].data[item];

                                let valueEx = extractValue(key, value);

                                //*TABLE VALUE
                                if(typeof value == "object") {
                                    const keyPassed = ["_id", "date_created", "des", "time", "description"]

                                    return (
                                        <>
                                            <span><b>{ valueEx.name }</b></span>
                                            <table>
                                                {
                                                    value.map((row, index) => {
                                                        if(index == 0) { //Header
                                                            return (
                                                                <tr>
                                                                    { 
                                                                        Object.keys(row).map(rowK => {
                                                                            if(keyPassed.indexOf(rowK) > -1) return;
                                                                            return <th style={{
                                                                                textAlign: 'left'
                                                                            }}>{ valueEx.valMap?.[rowK]?.name || rowK }</th>
                                                                        })
                                                                    }
                                                                </tr>
                                                            )
                                                        }

                                                        return ( //Body
                                                            <tr>
                                                                {
                                                                    Object.keys(row).map(rowK => {
                                                                        if(keyPassed.indexOf(rowK) > -1) return;
                                                                        return <td>{ row[rowK] }</td>
                                                                    }) 
                                                                }
                                                            </tr>
                                                        )
                                                    })
                                                }
                                            </table>
                                        </>
                                    )
                                };

                                //*SINGLE VALUE
                                return (
                                    <span key={`history-detail-item-${index}`}>
                                        <b>{ valueEx.name }: </b>
                                        <span>{ valueEx.value }</span>
                                    </span>
                                )
                            })
                        }
                        <hr/>
                        <button className="btn blue" onClick={handleRestore}>Khi phc</button>
                    </PaperCard>
                </div>
                <div className="col">
                    <TimeLine/>
                    {
                        histories.map((item, index) => {
                            return (
                                <PaperCard key={`history-card-${index}`} onClick={() => setHistorySelected(index)}>
                                    <span>{ item.author.ho_ten }</span>
                                    <span><b>{ HistoryCode[item.code] }</b></span>
                                    <span><b>Thi gian: </b>{ item.date_created }</span>
                                </PaperCard>
                            )
                        })
                    }
                </div>
            </div>     
        </div>
    )
}
