import React, { useEffect, useState, useRef } from 'react';
import { numberWithCommas } from '@functions/Global';
import Logo from '@styles/resources/Logo.png';
import {RiArrowLeftSLine} from 'react-icons/ri';
import {Link} from 'react-router-dom';
import API from '@api';

const BillPrintScreen = ({ match, history }) => {
    const [data, setData] = useState(null);
    const [isPrinting, setIsPrinting] = useState(false);
    const billRef = useRef(0);

    useEffect(() => {
        API.Bill.get(match.params.id).then(billRes => {
            console.log(billRes.data);
            setData(billRes.data);
        }).catch(e => console.log(e));
    }, []);

    const handlePrint = e => {
        e.target.style.display = 'none';
        setIsPrinting(true);

        setTimeout(() => {
            window.print();

            setIsPrinting(false);
        }, 600);
    };

    const handleSaveImage = async () => {
        setIsPrinting(true);

        setTimeout(async () => {
            let bill = billRef.current;
            let canvas = await window.html2canvas(bill);
            let image = canvas.toDataURL("image/jpeg").replace("image/jpeg", "image/octet-stream");  
            const link = document.createElement("a");

            link.href = image;
            link.download = "bill.jpg";
            link.click();

            setIsPrinting(false);
        }, 600);
    }

    if(!data) return null;

    const gioitinh = ["B Trai", "B Gi"]
    
    return(
        <div className="view bill_print" ref={billRef} style={{
            padding: 32
        }}>
            { !isPrinting && <h1 onClick={history.goBack} className="view_header" style={{
                textDecoration: 'none',
                cursor: 'pointer',
                width: "100%", 
                textAlign: "left", 
                marginTop: "32px", 
                marginBottom : "64px",
            }}><RiArrowLeftSLine className="icon"/>In Ha n</h1> }
            <span style={{textAlign: "center", width: "100%", whiteSpace: "pre-wrap"}}>{ data.chi_nhanh?.dia_chi }</span>
            <h1>HA N</h1>
            <span>Chi Nhnh: <span>{data.chi_nhanh?.ten_chi_nhanh}
            </span></span>
            <span>Ngy Chp: <span>{data.gio_chup} {data.ngay_chup}</span></span>
            <span>Ngy Giao Hnh: <span>{data.ngay_giao_hinh}</span></span>
            <span>Tn Khch Hng: <span>{data.ten_khach_hang}</span></span>
            <span>ST: <span>{data.so_dien_thoai}</span></span>
            <span>a Ch: <span>{data.dia_chi}</span></span>
            <span>H Tn B:  <span>{data.ho_ten_be}</span></span>
            <span>Ngy Sinh:  <span>{data.ngay_sinh_be}</span></span>
            <span>Gii Tnh: <span>{gioitinh[data.gioi_tinh_be]} - {data.so_tuoi}</span></span>

            <hr/>

            <table>
                <tr>
                    <th>Sn Phm</th>
                    <th>n Gi</th>
                    <th>S Lng</th>
                    <th>Gim Gi</th>
                    <th>Tng</th>
                </tr>
                {data.products.map((data, i) => {
                    return(
                        <tr>
                            <td>{data.ten_goi}</td>
                            <td>{numberWithCommas(parseInt(data.gia))}</td>
                            <td>{data.so_luong}</td>
                            <td>{data.giam_gia ? data.giam_gia : 0} </td>
                            <td>{numberWithCommas(parseInt(data.gia) * parseInt(data.so_luong) - (data.giam_gia ? data.giam_gia : 0))} </td>
                        </tr>
                    )
                })}
            </table>
            
            <hr/>

            <span>TNG CNG: <span>{numberWithCommas(parseInt(data.tong))} </span></span>
            <span>T CC: <span>{numberWithCommas(parseInt(data.dat_coc[0].value))} </span></span>
            {
                data.thanh_toan.map((item, index) => {
                    return <span>THANH TON LN {index + 1}: <span>{numberWithCommas(parseInt(item.value))} </span></span>
                })
            }
            <span>CN N: <span>{numberWithCommas(parseInt(data.con_no))} </span></span>

            {
                !isPrinting && (
                    <div style={{
                        display: 'flex'
                    }}>
                        <button className="btn blue" onClick={handlePrint}>IN</button>
                        <button className="btn blue" onClick={handleSaveImage}>LU NH</button>
                    </div>
                )
            }
        </div>
    )
}

export default BillPrintScreen;