import Axios from '@api/Axios';

const PackageTypeAPI = {
    add: async(data) => await Axios.post('package_types', data),
    get: async(id) => await Axios.get(`package_types/${id}`),
    getSlug: async(slug) => await Axios.get(`package_types/slug/${slug}`),
    getAll: async() => await Axios.get(`package_types`),
    update: async(id, data) => await Axios.put(`package_types/${id}`, data),
    remove: async(id) =>  await Axios.delete(`package_types/${id}`),
}

export default PackageTypeAPI;