import React, { useEffect } from 'react';

export const CloseTransactionScreen = ({}) => {
    useEffect(() => {
        window.close();
    }, []);

    return (
        <div>
            <h1>Close Transaction</h1>
        </div>
    )
}

export const TransactionSuccessScreen = () => {
    const handleClose = () => {
        window.close();
    }

    return (
        <div>
            <h1>Giao dch thnh cng, hy ti li website  cp nht gi mi nh!</h1>
            <button onClick={handleClose}>ng</button>
        </div>
    )
}