import Axios from '@api/Axios';

const BranchAPI = {
    add: async(data) => await Axios.post('branches', data),
    get: async(id) => await Axios.get(`branches/${id}`),
    getAll: async() => await Axios.get(`branches`),
    update: async(id, data) => await Axios.put(`branches/${id}`, data),
    remove: async(id) =>  await Axios.delete(`branches/${id}`),
}

export default BranchAPI;