import React, {Component} from 'react';
import {PermissionModal, PermissionEditModal} from '@components/Modals';
import { FormModal } from "@components";
import {DefaultHeaderBar} from '@components/Header_bar';
import API from '@api';

class TabBar extends Component {
    constructor(props) {
        super(props);

        this.state = {
            tab_active: this.props.tab
        }
    }

    handleChangeTab = (tab) => {
        this.setState({
            tab_active: tab
        });

        this.props.onChangeTab(tab);
    }

    render() {

        return(
            <div className="tab_bar">
                {this.props.data.map((data , i) => {
                    return(
                        <div key={i} onClick={() => this.handleChangeTab(i)} className={this.state.tab_active === i ? "tab active" : "tab"}>
                            <span className="tab_title">{data.title}</span>
                        </div>
                    )
                })}
            </div>
        )
    }
}

class TabContentContainer extends Component {
    constructor(props) {
        super(props);
        this.state =  {
            empty: [0,0,0,0]
        }
    }

    render() {
        let data = this.props.data;

        return(
            <div className="tab_content_container">
                <div className="tab_content_header_container" style={{paddingLeft: 0}}>
                    <h1 className="tab_content_header" style={{position: 'relative', marginRight: 46}}>Danh sch chc nng:</h1>
                    <div className="tab_header_right_item_container" style={{flex: 1}}>
                        <span className="tab_header_item_title">Truy cp</span>
                        <span className="tab_header_item_title">Gii hn s lng ( -1 : Khng gii hn )</span>
                    </div>
                </div>
                <div className="tab_content" style={{alignItems: 'baseline'}}>
                    <div className="tab_side_bar">
                        {
                            Object.values(data.features).map((item, index) => {
                                return (
                                    <span className="tab_side_bar_item" key={`side-bar-item-${index}`} style={{height: 31, marginTop: 24}}>{item.title}</span>
                                );
                            })
                        }
                    </div>
                    <div className="tab_tick_container" style={{position: 'relative', flex: 1, justifyContent: 'start'}}>
                        <div className="tab_tick" style={{justifyContent: 'start', flexDirection: 'column', gap: 24, margin: 0, marginLeft: -24}}>
                            {
                                Object.values(data.features).map((item, index) => {
                                    return (
                                        <div className="tick_row" style={{display: "flex", alignItems: "center"}} key={`tich-row-${index}`}>
                                            <div className="checkbox_container" style={{marginLeft: 42}}>
                                                <input type="checkbox" onChange={(e) => this.props.onChange(e, Object.keys(data.features)[index], 'available')} defaultChecked={item.available || false}/>
                                            </div>
                                            <div className="input_container" style={{marginLeft: 80}}>
                                                <input type="number" onChange={(e) => this.props.onChange(e, Object.keys(data.features)[index], 'limit')} className="textbox" defaultValue={item.limit || 0}/>
                                            </div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export class AdminSubscriptionScreen extends Component {
    constructor(props) {
        super(props);
        this.state = {
            is_modal_open: "false",
            tab_data: null,
            tab: 0,
            user_perms: null,
            data: null
        }     

        API.Permission.getUserPerm().then(res => {
            this.setState({
                user_perms: res.data.perms
            })
        }).catch(e => console.log(e));

        this.updateData();
    }

    onTabChange = async (tab) => {   
        this.setState({
            tab_data: null,
            tab: tab
        });

        API.Subscription.getAll().then(res => {
            this.setState({
                data: res.data,
                tab_data: res.data[tab]
            })
        }).catch(e => console.log(e));
    }

    handleOpenCreateModal = () => {
        this.setState ({
            is_modal_open: "true"
        })
    }

    handleOpenEditModal = () => {
        let data = this.state.tab_data;

        this.refs.ten_goi.value = data.title;
        this.refs.mo_ta.value = data.description;
        this.refs.gia.value = data.price;
        this.refs.phan_tram_giam.value = data.discount;
        this.refs.thoi_gian.value = data.duration;

        this.setState ({
            is_modal_open: "true",
            edit_id: data._id
        })
    }

    handleCloseModal = () => {
        this.setState ({
            is_modal_open: "false"
        })
    }

    handleChangeInput = (e, feature, ele) => {
        let value = e.target.value.toString() == 'on' ? e.target.checked : Number(e.target.value);

        this.setState({
            tab_data: {
                ...this.state.tab_data,
                features: {
                    ...this.state.tab_data.features,
                    [feature]: {
                        ...this.state.tab_data.features[feature],
                        [ele]: value
                    }
                }
            }
        });
    }

    handleSave = () => {
        let phanTramGiam = this.refs.phan_tram_giam.value;
        let gia = this.refs.gia.value;
        let strikePrice = gia/((100 - phanTramGiam)/100);

        let data = {
            title: this.refs.ten_goi.value,
            description: this.refs.mo_ta.value.trim().split("\n"),
            price: gia,
            strikePrice: strikePrice,
            discount: phanTramGiam,
            duration: this.refs.thoi_gian.value
        }

        if(this.state.edit_id) { //Update
            API.Subscription.update(this.state.edit_id, data).then(res => {
                this.updateData();
            }).catch(e => console.log(e));
        }else{ //Create
            API.Subscription.add(data).then(res => {
                this.updateData();
            }).catch(e => console.log(e));
        }

        this.updateData();
        this.handleCloseModal();
    }

    handleFeatureSave = async () => {
        let data = this.state.tab_data;

        API.Subscription.update(data._id, data).then(res => {
            this.updateData();
        }).catch(e => console.log(e));
    }

    updateData = async () => {
        await API.Subscription.getAll().then(res => {
            this.setState({
                data: res.data,
                tab_data: res.data[0]
            })
        }).catch(e => console.log(e));
    }

    handleRemove = async () => {
        let data = this.state.tab_data;

        API.Subscription.remove(data._id).then(res => {
            this.handleCloseModal();
            this.updateData();
        }).catch(e => console.log(e));
    }
    
    render() {
        if(!this.state.tab_data || !this.state.user_perms || !this.state.data) {
            return (
                <div className="view">
                    <DefaultHeaderBar  onGoBack={() => this.props.history.goBack()} title="Chc Nng Truy Cp" handleCreate={this.handleUpdateData}/>
                </div>
            )
        }
        return(
            <div className="view">
                <DefaultHeaderBar  
                    onGoBack={() => this.props.history.goBack()} 
                    create_btn_link={null} 
                    handleRemove={null} 
                    title="Qun L Gi Mua" 
                    handleEdit={ this.handleOpenEditModal} 
                    handleCreate={this.handleOpenCreateModal}
                    handleSave={this.handleFeatureSave}
                />
                <TabBar onChangeTab={this.onTabChange} tab={this.state.tab} data={this.state.data}/>
                <TabContentContainer data={this.state.tab_data} tab={this.state.tab} onChange={this.handleChangeInput}/>
                <FormModal 
                    title="Qun L Gi Mua"
                    is_open={this.state.is_modal_open} 
                    onClose={this.handleCloseModal}
                    onSave={this.handleSave}
                    onRemove={this.handleRemove}
                >
                     <div className="box">
                         <span className="title">Tn gi</span>
                         <input type="text" className="textbox" ref="ten_goi"/>
                     </div>
                     <div className="box">
                         <span className="title">M t</span>
                         <textarea type="text" className="textbox" ref="mo_ta"></textarea>
                     </div>
                     <div className="box">
                         <span className="title">Gi</span>
                         <input type="text" className="textbox" ref="gia"/>
                     </div>
                     <div className="box">
                         <span className="title">Phn trm gim</span>
                         <input type="text" className="textbox" ref="phan_tram_giam"/>
                     </div>
                     <div className="box">
                         <span className="title">Thi gian - ngy</span>
                         <input type="text" className="textbox" ref="thoi_gian"/>
                     </div>
                </FormModal>
            </div>
        )
    }
}