import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import { StickyTable, Row, Cell } from 'react-sticky-table';
import {HashSpiner} from './Spiners';
import {numberWithCommas, removeVietnameseTones, findWithAttr, salaryLoad, salaryCalculate} from '../functions/Global';
import API from "@api";

import '../styles/components/Table.scss';

class BillTableContainer extends Component {
    render() {
        if(this.props.data === null) {
          return(<div className="load_view"><HashSpiner/></div>)
        }
        return(
            <div className="table_view">
              <div className="table">
                <StickyTable>
                  <Row className="header">
                    <Cell>STT</Cell>
                    <Cell>Chi Nhnh</Cell>
                    <Cell>Gi Chp</Cell>
                    <Cell>Ngy Chp</Cell>
                    <Cell>Tn Khch Hng</Cell>
                    <Cell>ST</Cell>
                    <Cell>H Tn B</Cell>
                    <Cell>Tng</Cell>
                    <Cell> Thu</Cell>
                    <Cell>Cn N</Cell>
                    <Cell>Tnh Trng</Cell>
                    <Cell>Ngy Giao</Cell>
                    <Cell>Ghi Ch</Cell>
                  </Row>
                  {this.props.data.map((data, i) => {
                      let ngay_chup = data.ngay_chup
                      ngay_chup = ngay_chup ? ngay_chup.split("-")[0].length == 2 ? ngay_chup : ngay_chup.split("-")[2] + '-' + ngay_chup.split("-")[1] + '-' + ngay_chup.split("-")[0] : null

                      let ngay_giao_hinh = data.ngay_giao_hinh;

                      return(
                        <Row className="row">
                          <Cell><Link to={this.props.isDelete ? `bill_restore/${data._id}` : `bill_edit/${data._id}`}>{i + 1}</Link></Cell>
                          <Cell><div className={`row`} style={{backgroundColor: data.chi_nhanh.color}}><Link to={this.props.isDelete ? `bill_restore/${data._id}` : `bill_edit/${data._id}`}>{data.chi_nhanh.ten_chi_nhanh}</Link></div></Cell>
                          <Cell><Link to={this.props.isDelete ? `bill_restore/${data._id}` : `bill_edit/${data._id}`}>{data.gio_chup}</Link></Cell>
                          <Cell><Link to={this.props.isDelete ? `bill_restore/${data._id}` : `bill_edit/${data._id}`}>{ngay_chup}</Link></Cell>
                          <Cell><Link to={this.props.isDelete ? `bill_restore/${data._id}` : `bill_edit/${data._id}`}>{data.ten_khach_hang}</Link></Cell>
                          <Cell><Link to={this.props.isDelete ? `bill_restore/${data._id}` : `bill_edit/${data._id}`}>{data.so_dien_thoai}</Link></Cell>
                          <Cell><Link to={this.props.isDelete ? `bill_restore/${data._id}` : `bill_edit/${data._id}`}>{data.ho_ten_be}</Link></Cell>
                          <Cell><Link to={this.props.isDelete ? `bill_restore/${data._id}` : `bill_edit/${data._id}`}>{numberWithCommas(data.tong)} </Link></Cell>
                          <Cell><Link to={this.props.isDelete ? `bill_restore/${data._id}` : `bill_edit/${data._id}`}>{numberWithCommas(data.dat_coc_1 + data.thanh_toan_l1_1 + data.thanh_toan_l2_1 + (data.thanh_toan_l3_1 ? data.thanh_toan_l3_1 : 0))} </Link></Cell>
                          <Cell><Link to={this.props.isDelete ? `bill_restore/${data._id}` : `bill_edit/${data._id}`}>{numberWithCommas(data.con_no)} </Link></Cell>
                          <Cell><div className={`row`} style={{backgroundColor: data.tinh_trang?.color}}><Link to={this.props.isDelete ? `bill_restore/${data._id}` : `bill_edit/${data._id}`}>{data.tinh_trang?.ten_tinh_trang}</Link></div></Cell>
                          <Cell><Link to={this.props.isDelete ? `bill_restore/${data._id}` : `bill_edit/${data._id}`}>{ngay_giao_hinh}</Link></Cell>
                          <Cell><Link to={this.props.isDelete ? `bill_restore/${data._id}` : `bill_edit/${data._id}`}>{data.ghi_chu}</Link></Cell>
                        </Row>
                      )
                  }, this)}
                </StickyTable>
              </div>
            </div>
        )
    }
}

class SpendingTable extends Component {
    handleEdit = (id) => {
      this.props.handleOpenModal(id);
    }

    render() {
        return(
            <div className="table_view">
              <div className="table">
                <StickyTable>
                  <Row className="header">
                    <Cell style={{width: "32px"}}>STT</Cell>
                    <Cell>Ngy Chi</Cell>
                    <Cell>Ni Dung</Cell>
                    <Cell>S Tin</Cell>
                    <Cell>Trng Thi Thanh Ton</Cell>
                    <Cell>Ngi Chi</Cell>
                  </Row>
                  {this.props.data.map((data, i) => {
                    let date = data.date.split("T")[0];
                    date = date.split("-");
                    date = date[2] + "-" + date[1] + "-" + date[0];

                    return(
                      <Row className="row" onClick={() => this.handleEdit(data._id)}>
                        <Cell>{i + 1}</Cell>
                        <Cell>{date}</Cell>
                        <Cell>{data.noi_dung}</Cell>
                        <Cell>{data.so_tien ? numberWithCommas(data.so_tien) : 0} </Cell>
                        <Cell>{data.type == 0 ? "Chuyn khon" : "Tin mt"}</Cell>
                        <Cell>{data.user}</Cell>
                      </Row>
                    )
                  })}
                </StickyTable>
              </div>
            </div>
        )
    }
}

class SpendingTableAnalytics extends Component {
  render() {
      if(!this.props.data) {
        return <div></div>
      }

      return(
          <div className="table_view">
            <div className="table" style={{margin: "0"}}>
              <StickyTable>
                <Row className="header">
                  <Cell style={{width: "32px"}}>STT</Cell>
                  <Cell>Ngy Chi</Cell>
                  <Cell>Ni Dung</Cell>
                  <Cell>S Tin</Cell>
                  <Cell>Trng Thi Thanh Ton</Cell>
                  <Cell>Ngi Chi</Cell>
                </Row>
                {this.props.data.map((data, i) => {
                  let ngay_chi = data.date_created.split("T")[0].split("-");
                  ngay_chi = ngay_chi[0].length == 2 ? data.date_created.split("T")[0] : ngay_chi[2] + '-' + ngay_chi[1] + '-' + ngay_chi[0];

                  return(
                    <Row className="row">
                      <Cell>{i + 1}</Cell>
                      <Cell>{ngay_chi}</Cell>
                      <Cell>{data.noi_dung}</Cell>
                      <Cell>{data.so_tien ? numberWithCommas(data.so_tien) : 0} </Cell>
                      <Cell>{data.type == 0 ? "Chuyn khon" : "Tin mt"}</Cell>
                      <Cell>{data.user_id.ho_ten}</Cell>
                    </Row>
                  )
                })}
              </StickyTable>
            </div>
          </div>
      )
  }
}

