class AnalyticsTable extends Component {
  constructor(props) {
      super(props);
      this.state = {
          bill: null
      }

      API.Bill.getAll().then(res => {
        let data = res.data;

        this.setState({
          bill: data
        })
      });
  }

  render() {
    if(!this.props.data) {
      return(
        <div></div>
      )
    }

    return(
      <div className="table_view">
        <div className="table" style={{margin: "20px"}}>
          <StickyTable>
            <Row className="header">
              <Cell>STT</Cell>
              <Cell>Chi Nhnh</Cell>
              <Cell>Gi Chp</Cell>
              <Cell>Ngy Chp</Cell>
              <Cell>Tn Khch Hng</Cell>
              <Cell>ST</Cell>
              <Cell>H Tn B</Cell>
              <Cell>Tng</Cell>
              <Cell> Thu</Cell>
              <Cell>Thu Trong Ngy</Cell>
              <Cell>Cn N</Cell>
              <Cell>Tnh Trng</Cell>
              <Cell>Ghi Ch</Cell>
            </Row>
            {this.props.data.map((data, i) => {
                let ngay_chup = data.ngay_chup.split("-");
                ngay_chup = ngay_chup[0].length !== 2 ? ngay_chup[2] + '-' + ngay_chup[1] + '-' + ngay_chup[0] : ngay_chup[0] + '-' + ngay_chup[1] + '-' + ngay_chup[2]

                return(
                  <Row className="row">
                    <Cell><Link to={`bill_edit/${data._id}`}>{i + 1}</Link></Cell>
                    <Cell><div className="row" style={{backgroundColor: data.chi_nhanh.color}}><Link to={`/bill_edit/${data._id}`}>{data.chi_nhanh.ten_chi_nhanh}</Link></div></Cell>
                    <Cell><Link to={`bill_edit/${data._id}`}>{data.gio_chup}</Link></Cell>
                    <Cell><Link to={`bill_edit/${data._id}`}>{ngay_chup}</Link></Cell>
                    <Cell><Link to={`bill_edit/${data._id}`}>{data.ten_khach_hang}</Link></Cell>
                    <Cell><Link to={`bill_edit/${data._id}`}>{data.so_dien_thoai}</Link></Cell>
                    <Cell><Link to={`bill_edit/${data._id}`}>{data.ho_ten_be}</Link></Cell>
                    <Cell><Link to={`bill_edit/${data._id}`}>{numberWithCommas(data.tong)} </Link></Cell>
                    <Cell><Link to={`bill_edit/${data._id}`}>{numberWithCommas(data.daThu)} </Link></Cell>
                    <Cell><Link to={`bill_edit/${data._id}`}>{numberWithCommas(data.daThuToday)} </Link></Cell>
                    <Cell><Link to={`bill_edit/${data._id}`}>{numberWithCommas(data.con_no)} </Link></Cell>
                    <Cell><div className="row" style={{backgroundColor: data.tinh_trang?.color}}><Link to={this.props.isDelete ? `/bill_restore/${data._id}` : `/bill_edit/${data._id}`}>{data.tinh_trang?.ten_tinh_trang}</Link></div></Cell>
                    <Cell><Link to={`bill_edit/${data._id}`}>{data.ghi_chu}</Link></Cell>
                  </Row>
                )
            }, this)}
          </StickyTable>
        </div>
      </div>
    )
  }
}

