import React, {Component} from 'react';
import {DefaultHeaderBar} from '@components/Header_bar';
import {HashSpiner} from '@components/Spiners';
import {removeVietnameseTones} from '@functions/Global';
import { Table } from '@components';
import API from '@api';
import * as Icons from 'react-icons/io5';
import Excel from '@utils/Excel';

const TABLE_COLUMNS = [
    {
        key: "ten_chi_nhanh",
        title: "Chi nhnh",
    }, {
        key: "ten_khach_hang",
        title: "Tn khch hng",
    }, {
        key: "so_dien_thoai",
        title: "S in thoi",
    }, {
        key: "dia_chi",
        title: "a ch",
    }
]

class CustomerScreen extends Component {
    constructor(props) {
        super(props);

        this.state = {
            customers: null
        }

        API.Bill.getAll().then(res => {
            let data = res.data.map((data) => {
                if(!data.deleted) {
                    return {
                        ten_chi_nhanh: data.chi_nhanh.ten_chi_nhanh,
                        ten_khach_hang: data.ten_khach_hang,
                        so_dien_thoai: data.so_dien_thoai,
                        dia_chi: data.dia_chi,
                        date_created: data.date_created
                    }
                }
            });

            //Filter trung lap
            let tenKhCheckTemp = "";
            
            data.sort((a, b) => a.so_dien_thoai - b.so_dien_thoai);

            data = data.filter(kh => {
                let phoneNumber = kh?.so_dien_thoai?.trim();
                if(tenKhCheckTemp != phoneNumber) {
                    tenKhCheckTemp = phoneNumber;
                    return true;
                }

                return false;
            });

            data.sort((a, b) => a.date_created - b.date_created);

            this.setState ({
                customers: data
            })
        }).catch(e => console.log(e));
    }

    //THC HIN SEARCH
    handleSearch = (search_value) => {
        //RELOAD DATA IF SEARCH VALUE IS NULL
        if(search_value === "") {
            API.Bill.getAll().then(res => {
                let data = res.data.filter((data) => {
                    if(!data.deleted) {
                        return {
                            ten_chi_nhanh: data.chi_nhanh.ten_chi_nhanh,
                            ten_khach_hang: data.ten_khach_hang,
                            so_dien_thoai: data.so_dien_thoai,
                            dia_chi: data.dia_chi,
                        }
                    }
                })

                this.setState({
                    bill: data
                })     
            }).catch(e => console.log(e));
        }

        //RELOAD DATA
        API.Bill.getAll().then((res) => {
            //TM KHCH HNG OR TN B OR SS IN THOI
            let filtered_data = res.data.filter((data, i) => {
                const searchValue = removeVietnameseTones(search_value.toLowerCase());
                const tenKhachHang = removeVietnameseTones(data.ten_khach_hang.toLowerCase());
                const soDienThoai = data.so_dien_thoai;

                if((tenKhachHang.indexOf(searchValue) || soDienThoai.indexOf(searchValue)) && !data.deleted) {
                    return {
                        ten_chi_nhanh: data.chi_nhanh.ten_chi_nhanh,
                        ten_khach_hang: data.ten_khach_hang,
                        so_dien_thoai: data.so_dien_thoai,
                        dia_chi: data.dia_chi,
                    }
                }

                return false;
            })
  
            this.setState({
                bill: filtered_data
            })            
        }).catch(e => console.log(e));
    }

    //FILTER
    handleFilter = async (date) => {
        await API.Bill.getAll().then(res => {
            let data = res.data.map((data) => {
                if(!data.deleted) {
                    return {
                        ten_chi_nhanh: data.chi_nhanh.ten_chi_nhanh,
                        ten_khach_hang: data.ten_khach_hang,
                        so_dien_thoai: data.so_dien_thoai,
                        dia_chi: data.dia_chi,
                        date_created: data?.date_created
                    }
                }
            });

            data = data.filter(item => {
                if(item?.date_created) {
                    let month = new Date(item.date_created).getMonth();
                    let year = new Date(item.date_created).getFullYear();
                    let filterMonth = new Date(date).getMonth();
                    let filterYear = new Date(date).getFullYear();

                    return month == filterMonth && year == Number(filterYear);
                }
            });

            //Filter trung lap
            let tenKhCheckTemp = "";
            
            data.sort((a, b) => a.so_dien_thoai - b.so_dien_thoai);

            data = data.filter(kh => {
                let phoneNumber = kh?.so_dien_thoai?.trim();
                if(tenKhCheckTemp != phoneNumber) {
                    tenKhCheckTemp = phoneNumber;
                    return true;
                }

                return false;
            });

            data.sort((a, b) => a.date_created - b.date_created);

            this.setState ({
                customers: data
            })
        }).catch(e => console.log(e));
    }

    //DOWNLOAD CUSTOMERS LIST
    _handleDownloadList = async () => {
        //*Convert to Excel File
        let rowsData = this.state.customers.map((item, index) => ([
            index + 1,
            item?.ten_chi_nhanh,
            item?.ten_khach_hang,
            item?.so_dien_thoai,
            item?.dia_chi
        ]));
        let excelFile = Excel.createExcel([
            "STT",
            "Chi nhnh",
            "Tn khch hng",
            "S in thoi",
            "a ch"
        ], rowsData);

        // Convert and Download file
        let dateNow = new Date();
        let filename = `DS_Khach_Hang_${dateNow.getDate()}-${dateNow.getMonth() + 1}-${dateNow.getFullYear()}`;
        var blob = new Blob([excelFile], {
            type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
        });
        var element = document.createElement('a');
        element.setAttribute('href', window.URL.createObjectURL(blob));
        element.setAttribute('download', filename);
      
        element.style.display = 'none';
        document.body.appendChild(element);
      
        element.click();
      
        document.body.removeChild(element);
    }

    render() {
        if(!this.state.customers) {
            return(<div className="load_view"><HashSpiner/></div>)
        }

        return(
            <div>
                <DefaultHeaderBar 
                    onCustomerFilter={this.handleFilter}
                    actions={[
                        <button onClick={this._handleDownloadList}>
                            <Icons.IoDownloadOutline className="icon"/>
                            Ti danh sch KH
                        </button>
                    ]}
                    onSearchKeyPress={this.handleSearch}
                    title="Khch Hng"/>
                <Table
                    columns={TABLE_COLUMNS}
                    data={this.state.customers}
                />
            </div>
        )
    }
}

export default CustomerScreen;