import React, { useEffect, useState } from 'react';
import { DefaultHeaderBar } from '@components/Header_bar';
import { PaperCard, Button } from '@components';
import { useParams } from 'react-router-dom';
import Alert from '@utils/Alert';
import API from '@api';
import Socket from '@socket';
import "./styles.scss";

const HistoryCode = {
    "add" : " thm mi",
    "update" : " chnh sa:",
    "restore" : " khi phc"
};

const socket = new Socket();

const TimeLine = () => {
    return (
        <div className="timeline">
            <div className="timeline__dot"></div>
            <div className="timeline__date-container">
                <span className="timeline__date-container__value">Nov 3, 2022</span>
            </div>
        </div>
    )
}

export const TransactionManage = ({ history }) => {
    let { id } = useParams();
    const [transactions, setTransactions] = useState([]);
    const [transactionSelected, setTransactionSelected] = useState(0);

    useEffect(() => {
        fetchData();
        initSocket();
    }, []);

    const fetchData = async() => {
        let transactions = await API.Spending.getAll();
        transactions = transactions.data.reverse();

        setTransactions(transactions);
    }

    const initSocket = async() => {
        socket.Spending().onAdd(data => {
            fetchData();
        })
    }

    const handleConfirm = async(type) => {
        await API.Spending.confirm(transactions[transactionSelected]._id, type);
        fetchData();
    }

    return (
        <div className="view transactions">
            <DefaultHeaderBar  
                onGoBack={() => history.goBack()} 
                title="Qun L Giao Dch" 
            />  

            <div className="container">
                <div className="col">
                    <PaperCard>
                        <h1>Thng tin chi tit</h1>
                        <span><b>Trng thi: </b><span>{transactions[transactionSelected]?.status}</span></span>
                        <span><b>Ngi to: </b><span>{transactions[transactionSelected]?.user_id?.ho_ten}</span></span>
                        <span><b>Thi gian: </b><span>{transactions[transactionSelected]?.date_created}</span></span>
                        <hr/>
                        <span><b>S tin: </b><span>{transactions[transactionSelected]?.so_tien}</span></span>
                        <span><b>Loi thanh ton: </b><span>{transactions[transactionSelected]?.type}</span></span>
                        <span><b>Ngi nhn: </b><span>{transactions[transactionSelected]?.to_wallet}</span></span>
                        <span><b>Ni dung chi: </b><span>{transactions[transactionSelected]?.noi_dung}</span></span>
                        <hr/>
                        <div style={{display: 'flex', gap: 24}}>
                            <button disabled={transactions[transactionSelected]?.status != "pending"} className="btn red" onClick={() => handleConfirm("refused")} style={{flex: 1}}>T chi</button>
                            <button disabled={transactions[transactionSelected]?.status != "pending"} className="btn blue" onClick={() => handleConfirm("accepted")} style={{flex: 1}}>Duyt</button>
                        </div>
                    </PaperCard>
                </div>
                <div className="col">
                    <TimeLine/>
                    {
                        transactions.map((item, index) => {
                            return (
                                <PaperCard key={`history-card-${index}`} onClick={() => setTransactionSelected(index)}>
                                    <span>{ item.user_id.ho_ten }</span>
                                    <span>
                                        <b>Trng thi: </b>
                                        <span>{item.status}</span>
                                    </span>
                                </PaperCard>
                            )
                        })
                    }
                </div>
            </div>     
        </div>
    )
}
