import React, { useEffect, useState } from 'react';
import {DefaultHeaderBar} from '@components/Header_bar';
import { SubscriptionCard } from '@components';
import API from '@api';

import "./styles.scss";

const SubscriptionScreen = ({ history }) => {
    const [subscriptions, setSubscriptions] = useState([]);
    const [subscribed, setSubscribed] = useState(null);

    useEffect(() => {
        API.Subscription.getAll().then(res => {
            setSubscriptions(res.data);
        }).catch(e => console.log(e));

        API.Subscription.getCompany().then(res => {
            setSubscribed(res.data.subscription._id);
        }).catch(e => console.log(e));
    }, []);

    const handleSubscribe = async (id) => {
        await API.Subscription.createTransaction(id).then(result => {
            //Open popup and listen callback
            let transactionPopup = window.open(result.data.payUrl, "Test", "height=600,width=800");
            
            transactionPopup.onbeforeunload = () => {
                console.log("DONE");
            };

            setSubscribed(id);
        }).catch(e => console.log(e));

        //setSubscribed(id);
    }

    return (
        <div className="view view_subscription">
            <DefaultHeaderBar onGoBack={() => history.goBack()} title="Gi Mua"/>
            
            <div className="head-container">
                <h2>Gi ng K</h2>
                <p>In publishing and graphic design, Lorem ipsum is a placeholder text commonly</p>
            </div>

            <div className="subscription-container">
                {
                    subscriptions.map((item, index) => {
                        return (
                            <SubscriptionCard
                                title={item.title}
                                description={item.description}
                                price={item.price}
                                strikePrice={item.strikePrice}
                                save={item.discount}
                                key={`subscription-card-${index}`}
                                subscribed={subscribed === item._id}
                                onClick={() => handleSubscribe(item._id)}
                            />
                        )
                    })
                }
            </div>
        </div>
    )
}

export default SubscriptionScreen;