import React from 'react';
import { StickyTable, Row, Cell } from 'react-sticky-table';
import { StaffSalaryTable } from './StaffSalary';
import { PackageTable } from './Package';
import { ProductListTable } from './ProductList';
import { ChamCongTable } from './ChamCong';
import './styles.scss';

const Table = ({columns, data, onClick}) => {
    return(
        <div className="table_view">
          <div className="table">
            <StickyTable>
              <Row className="header">
                  <Cell style={{width: "32px"}}>STT</Cell>
                  {
                      columns.map((column, index) => {
                          return(
                              <Cell key={`column-${index}`}>{column.title}</Cell>
                          )
                      })
                  }
              </Row>
                  {data.map((data, i) => {
                    return(
                      <Row className="row" key={i} onClick={() => onClick(data._id,i)}>
                        <Cell>{i + 1}</Cell>
                        {
                          columns.map((column, index) => {
                            if(column.colored) {
                              return(
                                <Cell key={`row-${index}-${i}`}>
                                  <div 
                                    className={`row`} 
                                    style={{backgroundColor: data.color}}
                                  >
                                      {data[column.key]}
                                  </div>
                                </Cell>
                              )
                            }

                            return(
                                <Cell key={`row-${index}-${i}`}>{data?.[column.key]}</Cell>
                            )
                          })
                        }
                      </Row>
                    )
                  })}
            </StickyTable>
          </div>
        </div>
      )
}

export {
    StaffSalaryTable,
    PackageTable,
    Table,
    ProductListTable,
    ChamCongTable
}